// @ts-check
import * as PIXI from 'pixi.js';
import { rng } from '../../utils/rng.js';

/**
 * PIXI Particle System
 * Handles particle effects using PIXI Graphics
 */
class PixiParticleSystem {
  constructor() {
    this.particles = [];
    this.pool = [];
    this.poolSize = 200;
    this.enabled = true;
    this.container = new PIXI.Container();
    
    // Initialize pool
    for (let i = 0; i < this.poolSize; i++) {
      this.pool.push(new PixiParticle({ x: 0, y: 0 }));
    }
  }
  
  setEnabled(enabled) {
    this.enabled = enabled;
    if (!enabled) {
      // Clear all particles when disabled
      this.clear();
    }
  }

  getParticle(options) {
    let particle;
    if (this.pool.length > 0) {
      particle = this.pool.pop();
      // Recreate graphics if they were destroyed
      if (!particle.graphics) {
        particle.graphics = new PIXI.Graphics();
      }
      particle.reset(options);
    } else {
      particle = new PixiParticle(options);
    }
    return particle;
  }

  returnToPool(particle) {
    particle.isDead = true;
    // Clear graphics to prevent visual artifacts
    if (particle.graphics) {
      particle.graphics.clear();
      particle.graphics.visible = false;
      // Remove from container if it exists
      if (this.container.children.includes(particle.graphics)) {
        this.container.removeChild(particle.graphics);
      }
      // Destroy the graphics to prevent any lingering artifacts
      particle.graphics.destroy();
      particle.graphics = null;
    }
    this.pool.push(particle);
  }

  // Explosion effect for obstacle destruction
  explosion(x, y, color = 0x9966ff, count = 20) {
    if (!this.enabled) return;
    
    for (let i = 0; i < count; i++) {
      const angle = (Math.PI * 2 * i) / count;
      const speed = rng.randomFloat(2, 5);
      const particle = this.getParticle({
        x,
        y,
        vx: Math.cos(angle) * speed,
        vy: Math.sin(angle) * speed,
        life: rng.randomFloat(0.3, 0.8),
        size: rng.randomFloat(1, 3),
        color: this.hexToNumber(color),
        type: 'circle',
      });
      this.particles.push(particle);
      this.container.addChild(particle.graphics);
    }
  }

  // Enhanced hit sparks for bullet impacts with realistic physics
  hitSparks(x, y, color = 0x66ccff) {
    if (!this.enabled) return;
    
    // Create main spark burst with varied properties
    this.createSparkBurst(x, y, color, 15, 3, 10, 0.3, 0.7, 1.5, 4.5);
    
    // Create fast white sparks for high-energy impact
    this.createSparkBurst(x, y, 0xFFFFFF, 8, 8, 15, 0.15, 0.4, 0.8, 2.5);
    
    // Create slow glowing sparks for lingering effect
    this.createSparkBurst(x, y, color, 6, 1, 5, 0.5, 1.0, 2, 6);
    
    // Create tiny micro-sparks for detail
    this.createSparkBurst(x, y, 0xFFDD88, 10, 12, 20, 0.08, 0.25, 0.3, 1.2);
    
    // Add directional sparks based on bullet trajectory
    this.createDirectionalSparks(x, y, color);
  }
  
  // Helper method to create spark bursts with different properties
  createSparkBurst(x, y, color, count, minSpeed, maxSpeed, minLife, maxLife, minSize, maxSize) {
    for (let i = 0; i < count; i++) {
      const angle = rng.randomFloat(0, Math.PI * 2);
      const speed = rng.randomFloat(minSpeed, maxSpeed);
      const life = rng.randomFloat(minLife, maxLife);
      const size = rng.randomFloat(minSize, maxSize);
      
      const particle = this.getParticle({
        x: x + rng.randomFloat(-3, 3), // Increased spread
        y: y + rng.randomFloat(-3, 3),
        vx: Math.cos(angle) * speed,
        vy: Math.sin(angle) * speed,
        life: life,
        size: size,
        color: this.hexToNumber(color),
        type: 'circle',
        gravity: rng.randomFloat(0.15, 0.4), // Enhanced gravity
        friction: rng.randomFloat(0.95, 0.98), // Air resistance
        rotation: rng.randomFloat(0, Math.PI * 2), // Random rotation
        rotationSpeed: rng.randomFloat(-0.3, 0.3), // Rotation speed
      });
      this.particles.push(particle);
      this.container.addChild(particle.graphics);
    }
  }
  
  // Create directional sparks that follow bullet trajectory
  createDirectionalSparks(x, y, color) {
    // Get bullet direction from the last bullet that hit
    const bulletDirection = this.lastBulletDirection || { x: 1, y: 0 };
    
    // Create sparks in the direction of bullet travel
    for (let i = 0; i < 5; i++) {
      const spread = rng.randomFloat(-0.5, 0.5); // Small spread around direction
      const angle = Math.atan2(bulletDirection.y, bulletDirection.x) + spread;
      const speed = rng.randomFloat(8, 15);
      const life = rng.randomFloat(0.2, 0.5);
      const size = rng.randomFloat(1, 3);
      
      const particle = this.getParticle({
        x: x + rng.randomFloat(-1, 1),
        y: y + rng.randomFloat(-1, 1),
        vx: Math.cos(angle) * speed,
        vy: Math.sin(angle) * speed,
        life: life,
        size: size,
        color: this.hexToNumber(color),
        type: 'circle',
        gravity: rng.randomFloat(0.1, 0.2),
        friction: rng.randomFloat(0.96, 0.99),
      });
      this.particles.push(particle);
      this.container.addChild(particle.graphics);
    }
  }
  
  // Store bullet direction for directional sparks
  setBulletDirection(direction) {
    this.lastBulletDirection = direction;
  }

  // Hit flash for bullet impact (keeping original for compatibility)
  hitFlash(x, y, color = 0x66ccff) {
    if (!this.enabled) return;
    
    for (let i = 0; i < 4; i++) {
      const angle = rng.randomFloat(0, Math.PI * 2);
      const speed = rng.randomFloat(3, 5);
      const particle = this.getParticle({
        x,
        y,
        vx: Math.cos(angle) * speed,
        vy: Math.sin(angle) * speed,
        life: 0.3,
        size: 2,
        color: this.hexToNumber(color),
        type: 'circle',
      });
      this.particles.push(particle);
      this.container.addChild(particle.graphics);
    }
  }

  // Thrust particles for ship engine
  thrust(x, y, intensity = 1) {
    if (!this.enabled) return;
    
    const count = Math.floor(intensity * 3);
    for (let i = 0; i < count; i++) {
      const particle = this.getParticle({
        x: x + rng.randomFloat(-5, 5),
        y: y + rng.randomFloat(-5, 5),
        vx: rng.randomFloat(-8, -3),
        vy: rng.randomFloat(-2, 2),
        life: rng.randomFloat(0.2, 0.5),
        size: rng.randomFloat(2, 4),
        color: rng.randomChoice([0x66ccff, 0x9966ff, 0xff66cc]),
        alpha: 0.8,
        type: 'circle',
      });
      this.particles.push(particle);
      this.container.addChild(particle.graphics);
    }
  }

  // Energy trail for bullets
  bulletTrail(x, y) {
    if (!this.enabled) return;
    
    // Create multiple small particles for a more subtle trail
    const particleCount = 2; // Reduced count to minimize artifacts
    for (let i = 0; i < particleCount; i++) {
      const particle = this.getParticle({
        x: x + rng.randomFloat(-1, 1), // Smaller random offset
        y: y + rng.randomFloat(-0.5, 0.5),
        vx: rng.randomFloat(-0.3, 0), // Slower movement
        vy: rng.randomFloat(-0.2, 0.2),
        life: 0.15, // Even shorter life
        size: 0.8, // Smaller size
        color: 0x66ccff,
        alpha: 0.3, // More transparent
        type: 'circle',
      });
      this.particles.push(particle);
      this.container.addChild(particle.graphics);
    }
  }

  // Damage sparks for ship hit
  damageSparks(x, y, color = 0xff3333) {
    if (!this.enabled) return;
    
    for (let i = 0; i < 12; i++) {
      const angle = rng.randomFloat(0, Math.PI * 2);
      const speed = rng.randomFloat(4, 10);
      const particle = this.getParticle({
        x,
        y,
        vx: Math.cos(angle) * speed,
        vy: Math.sin(angle) * speed,
        life: 0.6,
        size: 2,
        color: this.hexToNumber(color),
        gravity: 0.2,
        type: 'line',
      });
      this.particles.push(particle);
      this.container.addChild(particle.graphics);
    }
  }

  update(deltaMultiplier = 1) {
    if (!this.enabled) return;
    
    for (let i = this.particles.length - 1; i >= 0; i--) {
      const particle = this.particles[i];
      particle.update(deltaMultiplier);

      if (particle.isDead) {
        // Ensure complete cleanup before returning to pool
        if (particle.graphics) {
          particle.graphics.clear();
          if (this.container.children.includes(particle.graphics)) {
            this.container.removeChild(particle.graphics);
          }
          particle.graphics.destroy();
        }
        this.returnToPool(particle);
        this.particles.splice(i, 1);
      }
    }
  }

  clear() {
    // Clean up all active particles
    this.particles.forEach(particle => {
      if (particle.graphics) {
        particle.graphics.clear();
        particle.graphics.visible = false;
        if (this.container.children.includes(particle.graphics)) {
          this.container.removeChild(particle.graphics);
        }
        particle.graphics.destroy();
        particle.graphics = null;
      }
    });
    this.particles = [];
    
    // Clean up pooled particles
    this.pool.forEach(particle => {
      if (particle.graphics) {
        particle.graphics.clear();
        particle.graphics.visible = false;
        if (this.container.children.includes(particle.graphics)) {
          this.container.removeChild(particle.graphics);
        }
        particle.graphics.destroy();
        particle.graphics = null;
      }
    });
    this.pool = [];
  }
  
  hexToNumber(hex) {
    if (typeof hex === 'string') {
      return parseInt(hex.replace('#', ''), 16);
    }
    return hex;
  }
  
  getContainer() {
    return this.container;
  }
}

/**
 * PIXI Particle
 */
class PixiParticle {
  constructor(options) {
    this.graphics = new PIXI.Graphics();
    this.reset(options);
  }

  reset(options) {
    this.x = options.x;
    this.y = options.y;
    this.vx = options.vx || 0;
    this.vy = options.vy || 0;
    this.life = options.life || 1;
    this.maxLife = this.life;
    this.size = options.size || 2;
    this.color = options.color || 0x66ccff;
    this.alpha = options.alpha || 1;
    this.gravity = options.gravity || 0;
    this.friction = options.friction || 1;
    this.rotation = options.rotation || 0;
    this.rotationSpeed = options.rotationSpeed || 0;
    this.type = options.type || 'circle';
    this.isDead = false;
    this.trail = []; // For particle trails
    this.maxTrailLength = 3;
    
    this.updateGraphics();
  }

  update(deltaMultiplier = 1) {
    // Store previous position for trail
    this.trail.push({ x: this.x, y: this.y });
    if (this.trail.length > this.maxTrailLength) {
      this.trail.shift();
    }
    
    // Apply physics
    this.x += this.vx * deltaMultiplier;
    this.y += this.vy * deltaMultiplier;
    this.vy += this.gravity * deltaMultiplier;
    
    // Apply friction (air resistance)
    this.vx *= this.friction;
    this.vy *= this.friction;
    
    // Apply rotation
    this.rotation += this.rotationSpeed * deltaMultiplier;
    
    // Decrease life
    this.life -= 0.016 * deltaMultiplier; // Roughly 60fps

    if (this.life <= 0) {
      this.isDead = true;
    }
    
    this.updateGraphics();
  }
  
    updateGraphics() {
      if (this.isDead) {
        // Ensure graphics are cleared when dead
        this.graphics.clear();
        this.graphics.visible = false;
        return;
      }

      this.graphics.clear();
      this.graphics.x = this.x;
      this.graphics.y = this.y;
      this.graphics.rotation = this.rotation;
      this.graphics.visible = true;

      const lifeRatio = this.life / this.maxLife;
      this.graphics.alpha = this.alpha * lifeRatio;

      // Draw particle trail
      if (this.trail.length > 1) {
        this.graphics.setStrokeStyle({ 
          width: Math.max(0.5, this.size * lifeRatio * 0.3), 
          color: this.color, 
          alpha: lifeRatio * 0.5 
        });
        this.graphics.moveTo(0, 0);
        for (let i = this.trail.length - 1; i >= 0; i--) {
          const trailPoint = this.trail[i];
          const trailAlpha = (i / this.trail.length) * lifeRatio * 0.3;
          this.graphics.lineTo(trailPoint.x - this.x, trailPoint.y - this.y);
        }
      }

      if (this.type === 'circle') {
        this.graphics.beginFill(this.color, lifeRatio);
        this.graphics.circle(0, 0, this.size * lifeRatio);
        this.graphics.endFill();
      } else if (this.type === 'line') {
        this.graphics.setStrokeStyle({ width: this.size * lifeRatio, color: this.color, alpha: lifeRatio });
        this.graphics.moveTo(0, 0);
        this.graphics.lineTo(-this.vx * 2, -this.vy * 2);
      }
    }
}

// Create global PIXI particle system
const pixiParticleSystem = new PixiParticleSystem();

// Export for ES modules
export { PixiParticle, PixiParticleSystem, pixiParticleSystem };
