// @ts-check
import { rng } from '../../utils/rng.js';

/**
 * Visual Effects Manager
 * Handles screen shake, flash effects, and other visual feedback
 */
class EffectsManager {
  constructor() {
    // Screen shake
    this.shakeX = 0;
    this.shakeY = 0;
    this.shakeIntensity = 0;
    this.shakeDuration = 0;
    this.shakeDecay = 0.9;
    this.screenShakeEnabled = true;

    // Flash effects
    this.flashAlpha = 0;
    this.flashColor = '#ffffff';
    this.flashDecay = 0.05;

    // Slow motion
    this.timeScale = 1;
    this.targetTimeScale = 1;
    this.timeScaleTransitionSpeed = 0.1;

    // Hit stop (brief pause for impact)
    this.hitStopDuration = 0;
  }

  /**
   * Trigger screen shake
   * @param {number} intensity - Shake intensity (pixels)
   * @param {number} duration - Shake duration (frames at 60fps)
   */
  screenShake(intensity = 5, duration = 10) {
    if (!this.screenShakeEnabled) return;
    this.shakeIntensity = Math.max(this.shakeIntensity, intensity);
    this.shakeDuration = Math.max(this.shakeDuration, duration);
  }
  
  setScreenShakeEnabled(enabled) {
    this.screenShakeEnabled = enabled;
  }

  /**
   * Trigger flash effect
   * @param {string} color - Flash color
   * @param {number} alpha - Initial flash alpha
   */
  flash(color = '#ffffff', alpha = 0.5) {
    this.flashColor = color;
    this.flashAlpha = Math.max(this.flashAlpha, alpha);
  }

  /**
   * Set time scale (slow motion effect)
   * @param {number} scale - Time scale (0.5 = half speed, 1 = normal)
   * @param {number} duration - Duration in frames
   */
  setTimeScale(scale, duration = 30) {
    this.targetTimeScale = scale;
    this.timeScaleTransitionSpeed = Math.abs(this.timeScale - scale) / duration;
  }

  /**
   * Trigger hit stop (brief pause for impact)
   * @param {number} frames - Number of frames to pause
   */
  hitStop(frames = 3) {
    this.hitStopDuration = Math.max(this.hitStopDuration, frames);
  }

  update() {
    // Update screen shake
    if (this.shakeDuration > 0) {
      this.shakeX = rng.randomFloat(-this.shakeIntensity, this.shakeIntensity);
      this.shakeY = rng.randomFloat(-this.shakeIntensity, this.shakeIntensity);
      this.shakeIntensity *= this.shakeDecay;
      this.shakeDuration--;

      if (this.shakeDuration <= 0) {
        this.shakeX = 0;
        this.shakeY = 0;
        this.shakeIntensity = 0;
      }
    }

    // Update flash effect
    if (this.flashAlpha > 0) {
      this.flashAlpha -= this.flashDecay;
      if (this.flashAlpha < 0) this.flashAlpha = 0;
    }

    // Update time scale
    if (this.timeScale !== this.targetTimeScale) {
      const diff = this.targetTimeScale - this.timeScale;
      if (Math.abs(diff) < this.timeScaleTransitionSpeed) {
        this.timeScale = this.targetTimeScale;
      } else {
        this.timeScale += Math.sign(diff) * this.timeScaleTransitionSpeed;
      }
    }

    // Update hit stop
    if (this.hitStopDuration > 0) {
      this.hitStopDuration--;
    }
  }

  /**
   * Apply screen shake transformation to canvas context
   * @param {CanvasRenderingContext2D} ctx
   */
  applyShake(ctx) {
    if (this.shakeX !== 0 || this.shakeY !== 0) {
      ctx.translate(this.shakeX, this.shakeY);
    }
  }

  /**
   * Draw flash overlay
   * @param {CanvasRenderingContext2D} ctx
   * @param {number} width
   * @param {number} height
   */
  drawFlash(ctx, width, height) {
    if (this.flashAlpha > 0) {
      ctx.save();
      ctx.fillStyle = this.flashColor;
      ctx.globalAlpha = this.flashAlpha;
      ctx.fillRect(0, 0, width, height);
      ctx.restore();
    }
  }

  /**
   * Check if currently in hit stop
   * @returns {boolean}
   */
  isHitStop() {
    return this.hitStopDuration > 0;
  }

  /**
   * Get current time scale
   * @returns {number}
   */
  getTimeScale() {
    return this.isHitStop() ? 0 : this.timeScale;
  }

  /**
   * Reset all effects
   */
  reset() {
    this.shakeX = 0;
    this.shakeY = 0;
    this.shakeIntensity = 0;
    this.shakeDuration = 0;
    this.flashAlpha = 0;
    this.timeScale = 1;
    this.targetTimeScale = 1;
    this.hitStopDuration = 0;
  }
}

// Create global effects manager
const effectsManager = new EffectsManager();

// Export for ES modules
export { EffectsManager, effectsManager };
