// @ts-check

/**
 * Easing functions for smooth animations
 */
export const Easing = {
  linear: t => t,
  easeInQuad: t => t * t,
  easeOutQuad: t => t * (2 - t),
  easeInOutQuad: t => (t < 0.5 ? 2 * t * t : -1 + (4 - 2 * t) * t),
  easeInCubic: t => t * t * t,
  easeOutCubic: t => (--t) * t * t + 1,
  easeInOutCubic: t =>
    t < 0.5 ? 4 * t * t * t : (t - 1) * (2 * t - 2) * (2 * t - 2) + 1,
  easeInElastic: t => {
    const c4 = (2 * Math.PI) / 3;
    return t === 0 ? 0 : t === 1 ? 1 : -Math.pow(2, 10 * t - 10) * Math.sin((t * 10 - 10.75) * c4);
  },
  easeOutElastic: t => {
    const c4 = (2 * Math.PI) / 3;
    return t === 0 ? 0 : t === 1 ? 1 : Math.pow(2, -10 * t) * Math.sin((t * 10 - 0.75) * c4) + 1;
  },
  easeOutBounce: t => {
    const n1 = 7.5625;
    const d1 = 2.75;
    if (t < 1 / d1) {
      return n1 * t * t;
    } else if (t < 2 / d1) {
      return n1 * (t -= 1.5 / d1) * t + 0.75;
    } else if (t < 2.5 / d1) {
      return n1 * (t -= 2.25 / d1) * t + 0.9375;
    } else {
      return n1 * (t -= 2.625 / d1) * t + 0.984375;
    }
  },
};

/**
 * @typedef {Object} TweenOptions
 * @property {number} duration - Duration in milliseconds
 * @property {Function} [easing] - Easing function
 * @property {Function} [onUpdate] - Callback on each frame
 * @property {Function} [onComplete] - Callback when tween completes
 */

export class Tween {
  /**
   * @param {Object} target - Object to tween
   * @param {Object} to - Target values
   * @param {TweenOptions} options - Tween options
   */
  constructor(target, to, options) {
    this.target = target;
    this.to = to;
    this.from = {};
    this.duration = options.duration || 1000;
    this.easing = options.easing || Easing.linear;
    this.onUpdate = options.onUpdate;
    this.onComplete = options.onComplete;
    this.startTime = null;
    this.isComplete = false;
    this.isActive = true;

    // Store initial values
    for (const key in to) {
      this.from[key] = target[key];
    }
  }

  update(currentTime) {
    if (!this.isActive || this.isComplete) return false;

    if (this.startTime === null) {
      this.startTime = currentTime;
    }

    const elapsed = currentTime - this.startTime;
    const progress = Math.min(elapsed / this.duration, 1);
    const t = this.easing(progress);

    // Update target values
    for (const key in this.to) {
      const from = this.from[key];
      const to = this.to[key];
      this.target[key] = from + (to - from) * t;
    }

    if (this.onUpdate) {
      this.onUpdate(this.target, t);
    }

    if (progress >= 1) {
      this.isComplete = true;
      if (this.onComplete) {
        this.onComplete(this.target);
      }
      return false;
    }

    return true;
  }

  stop() {
    this.isActive = false;
    this.isComplete = true;
  }
}
