// utils/browserSupport.js

// Safari detection
export function isSafari() {
    const ua = navigator.userAgent.toLowerCase();
    return ua.includes('safari') && !ua.includes('chrome') && !ua.includes('chromium');
}

// iOS Safari detection
export function isIOSSafari() {
    const ua = navigator.userAgent.toLowerCase();
    return /iphone|ipad|ipod/.test(ua) && ua.includes('safari') && !ua.includes('chrome');
}

// Mobile Safari detection
export function isMobileSafari() {
    const ua = navigator.userAgent.toLowerCase();
    return /iphone|ipad|ipod/.test(ua) && ua.includes('safari');
}

export function checkWebGLSupport() {
    try {
        const canvas = document.createElement('canvas');
        const gl = canvas.getContext('webgl2') || 
                  canvas.getContext('webgl') || 
                  canvas.getContext('experimental-webgl');
        
        if (!gl) {
            return false;
        }

        // Additional WebGL capability checks
        const vertexShader = gl.createShader(gl.VERTEX_SHADER);
        const fragmentShader = gl.createShader(gl.FRAGMENT_SHADER);
        const program = gl.createProgram();

        if (!vertexShader || !fragmentShader || !program) {
            return false;
        }

        gl.deleteShader(vertexShader);
        gl.deleteShader(fragmentShader);
        gl.deleteProgram(program);

        return true;
    } catch (e) {
        console.warn('WebGL support check failed:', e);
        return false;
    }
}

// Safari-specific WebGL support check
export function checkSafariWebGLSupport() {
    if (!isSafari()) {
        return checkWebGLSupport();
    }
    
    try {
        const canvas = document.createElement('canvas');
        // Safari requires specific WebGL context creation
        const gl = canvas.getContext('webgl') || canvas.getContext('experimental-webgl');
        
        if (!gl) {
            return false;
        }

        // Safari-specific WebGL feature checks
        const extensions = gl.getSupportedExtensions();
        const hasRequiredExtensions = extensions && (
            extensions.includes('OES_texture_float') ||
            extensions.includes('WEBGL_lose_context')
        );

        // Test basic WebGL functionality
        const vertexShader = gl.createShader(gl.VERTEX_SHADER);
        const fragmentShader = gl.createShader(gl.FRAGMENT_SHADER);
        
        if (!vertexShader || !fragmentShader) {
            return false;
        }

        gl.deleteShader(vertexShader);
        gl.deleteShader(fragmentShader);

        return true;
    } catch (e) {
        console.warn('Safari WebGL support check failed:', e);
        return false;
    }
}

// Touch event support detection
export function checkTouchSupport() {
    return 'ontouchstart' in window || 
           navigator.maxTouchPoints > 0 || 
           window.DocumentTouch && document instanceof window.DocumentTouch;
}

// Safari-specific touch support
export function checkSafariTouchSupport() {
    if (!isSafari()) {
        return checkTouchSupport();
    }
    
    // Safari has specific touch event requirements
    return 'ontouchstart' in window || 
           (navigator.maxTouchPoints && navigator.maxTouchPoints > 0) ||
           (window.DeviceMotionEvent && typeof window.DeviceMotionEvent.requestPermission === 'function');
}

export function checkSystemRequirements() {
    const requirements = {
        webgl: isSafari() ? checkSafariWebGLSupport() : checkWebGLSupport(),
        canvas2d: !!document.createElement('canvas').getContext('2d'),
        supportedBrowser: !!(window.requestAnimationFrame && window.performance),
        touchSupport: isSafari() ? checkSafariTouchSupport() : checkTouchSupport(),
        safari: isSafari(),
        iosSafari: isIOSSafari(),
        mobileSafari: isMobileSafari()
    };
    
    return {
        ...requirements,
        canRunGame: requirements.canvas2d && requirements.supportedBrowser,
        canUseWebGL: requirements.webgl,
        canUseTouch: requirements.touchSupport
    };
}