// @ts-check
import { Tween, Easing } from './Tween.js';

class TweenManager {
  constructor() {
    this.tweens = [];
  }

  /**
   * @param {Object} target
   * @param {Object} to
   * @param {TweenOptions} options
   * @returns {Tween}
   */
  to(target, to, options) {
    const tween = new Tween(target, to, options);
    this.tweens.push(tween);
    return tween;
  }

  update(currentTime) {
    for (let i = this.tweens.length - 1; i >= 0; i--) {
      const tween = this.tweens[i];
      const isActive = tween.update(currentTime);
      if (!isActive) {
        this.tweens.splice(i, 1);
      }
    }
  }

  clear() {
    this.tweens.forEach(tween => tween.stop());
    this.tweens = [];
  }
}

// Create global tween manager
const tweenManager = new TweenManager();

// Export for ES modules
export { Tween, TweenManager, Easing, tweenManager };
