// @ts-check
import * as PIXI from 'pixi.js';
import { config } from '../config/game.js';

class PixiShip {
    constructor(x, y, shipImage, pixiApp, width, height) {
        this.shipImage = shipImage;
        this.pixiApp = pixiApp;
        this.appWidth = width;
        this.appHeight = height;
        
        // Fixed size to preserve SVG aspect ratio (no stretching)
        this.width = 60;  // Fixed width in pixels
        this.height = 60; // Fixed height in pixels (1:1 aspect ratio for rocket SVG)
        
        // Position as percentage of canvas
        this.xPercent = config.SHIP_X_PERCENT;
        this.yPercent = config.SHIP_Y_PERCENT;
        
        // Create PixiJS sprite
        this.sprite = new PIXI.Sprite(shipImage);
        this.sprite.width = this.width;
        this.sprite.height = this.height;
        this.sprite.anchor.set(0.5, 0.5); // Center anchor for rotation
        
        // Create container for ship and effects
        this.container = new PIXI.Container();
        this.container.addChild(this.sprite);
        
        // Add ambient glow effect
        this.ambientGlow = new PIXI.Graphics();
        this.container.addChildAt(this.ambientGlow, 0); // Add behind ship
        
        // Engine glow effect
        this.engineGlow = new PIXI.Graphics();
        this.container.addChild(this.engineGlow);
        
        this.updatePosition();
        
        // Physics state
        this.velocity = 0;              // Current velocity (pixels per frame)
        this.acceleration = 0;          // Current acceleration
        this.thrust = 0;                // Current thrust force
        this.targetThrust = 0;          // Target thrust (for smoothing)
        
        // Physics constants from config (with difficulty scaling)
        const difficultyMultiplier = config.getDifficultyMultiplier();
        this.gravity = config.SHIP_GRAVITY * difficultyMultiplier;
        this.thrustForce = config.SHIP_THRUST_FORCE * difficultyMultiplier;
        this.downThrustForce = config.SHIP_DOWN_THRUST_FORCE * difficultyMultiplier;
        this.drag = config.SHIP_DRAG_COEFFICIENT * difficultyMultiplier;
        this.terminalVelocityUp = config.SHIP_TERMINAL_VELOCITY_UP * difficultyMultiplier;
        this.terminalVelocityDown = config.SHIP_TERMINAL_VELOCITY_DOWN * difficultyMultiplier;
        this.thrustSmoothing = config.SHIP_THRUST_SMOOTHING;
        
        // Visual state (separate from physics)
        this.tilt = 0;                  // Current tilt angle
        this.targetTilt = 0;            // Target tilt angle
        this.tiltSmoothing = config.SHIP_TILT_SMOOTHING;
        this.tiltFactor = config.SHIP_TILT_FACTOR;
        
        // Bob animation
        this.bobOffset = 0;
        this.bobSpeed = config.SHIP_BOB_SPEED;
        this.bobAmplitude = config.SHIP_BOB_AMPLITUDE;
        this.time = 0;
        
        // Visual settings
        this.engineGlowEnabled = true;
        
        // Initialize visual effects
        this.updateAmbientGlow();
        this.updateEngineGlow();
    }
    
    updatePosition() {
        // Only update position, not size
        this.x = this.appWidth * this.xPercent;
        this.y = this.appHeight * this.yPercent;
        this.container.x = this.x;
        this.container.y = this.y;
    }

    update(deltaMultiplier = 1) {
        // 1. Smooth thrust interpolation
        this.thrust += (this.targetThrust - this.thrust) * this.thrustSmoothing;
        
        // 2. Calculate acceleration from forces
        this.acceleration = this.gravity + this.thrust;
        
        // 3. Apply drag (air resistance) - proportional to velocity squared
        const dragForce = this.velocity * Math.abs(this.velocity) * this.drag;
        this.acceleration -= dragForce;
        
        // 4. Update velocity with acceleration
        this.velocity += this.acceleration * deltaMultiplier;
        
        // 5. Clamp velocity to terminal limits
        this.velocity = Math.max(
            this.terminalVelocityUp, 
            Math.min(this.terminalVelocityDown, this.velocity)
        );
        
        // 6. Update position
        this.y += this.velocity * deltaMultiplier;
        this.container.y = this.y;
        
        // 7. Update visual tilt (smooth interpolation)
        this.targetTilt = this.velocity * this.tiltFactor;
        this.tilt += (this.targetTilt - this.tilt) * this.tiltSmoothing;
        
        // 8. Bob animation (visual only)
        this.time += this.bobSpeed * deltaMultiplier;
        this.bobOffset = Math.sin(this.time) * this.bobAmplitude;
        
        // Update visual effects
        this.updateVisuals();
    }
    
    updateVisuals() {
        // Apply tilt rotation
        const clampedTilt = Math.min(Math.max(this.tilt, -30), 30);
        this.container.rotation = clampedTilt * Math.PI / 180;
        
        // Apply bob offset
        this.sprite.y = this.bobOffset;
        
        // Update engine glow
        this.updateEngineGlow();
    }
    
    updateAmbientGlow() {
        this.ambientGlow.clear();
        
        // Create radial gradient effect
        this.ambientGlow.beginFill(0x66CCFF, 0.3);
        this.ambientGlow.drawCircle(0, 0, this.width * 0.8);
        this.ambientGlow.endFill();
        
        // Add purple tint
        this.ambientGlow.beginFill(0x9966FF, 0.2);
        this.ambientGlow.drawCircle(0, 0, this.width * 0.6);
        this.ambientGlow.endFill();
    }
    
    updateEngineGlow() {
        if (!this.engineGlowEnabled) {
            this.engineGlow.visible = false;
            return;
        }
        
        this.engineGlow.visible = true;
        this.engineGlow.clear();
        
        const thrustIntensity = Math.abs(this.thrust) / Math.max(this.thrustForce, this.downThrustForce);
        const glowAlpha = 0.5 + thrustIntensity * 0.5;
        
        // Engine glow effect with cyan/purple gradient
        const engineWidth = 20 + thrustIntensity * 10;
        
        // Main glow
        this.engineGlow.beginFill(0x66CCFF, glowAlpha);
        this.engineGlow.drawRect(-this.width/2 - engineWidth, -this.height/2, engineWidth, this.height);
        this.engineGlow.endFill();
        
        // Purple accent
        this.engineGlow.beginFill(0x9966FF, glowAlpha * 0.7);
        this.engineGlow.drawRect(-this.width/2 - engineWidth/2, -this.height/2, engineWidth/2, this.height);
        this.engineGlow.endFill();
    }

    applyLift() {
        // Apply upward thrust force
        this.targetThrust = -this.thrustForce;
    }

    applyDownForce() {
        // Apply downward thrust force
        this.targetThrust = this.downThrustForce;
    }
    
    releaseThrust() {
        // Release thrust, let gravity and drag take over
        this.targetThrust = 0;
    }
    
    getThrustIntensity() {
        // Get normalized thrust intensity for visual effects (0-1)
        return Math.abs(this.thrust) / Math.max(this.thrustForce, this.downThrustForce);
    }
    
    setEngineGlowEnabled(enabled) {
        this.engineGlowEnabled = enabled;
        this.updateEngineGlow();
    }
    
    updatePhysicsForDifficulty() {
        // Recalculate physics constants with current difficulty
        const difficultyMultiplier = config.getDifficultyMultiplier();
        this.gravity = config.SHIP_GRAVITY * difficultyMultiplier;
        this.thrustForce = config.SHIP_THRUST_FORCE * difficultyMultiplier;
        this.downThrustForce = config.SHIP_DOWN_THRUST_FORCE * difficultyMultiplier;
        this.drag = config.SHIP_DRAG_COEFFICIENT * difficultyMultiplier;
        this.terminalVelocityUp = config.SHIP_TERMINAL_VELOCITY_UP * difficultyMultiplier;
        this.terminalVelocityDown = config.SHIP_TERMINAL_VELOCITY_DOWN * difficultyMultiplier;
    }

    isOutOfBounds() {
        return this.y + this.height > this.appHeight || this.y < 0;
    }

    reset() {
        this.updatePosition();
        
        // Reset physics state
        this.velocity = 0;
        this.acceleration = 0;
        this.thrust = 0;
        this.targetThrust = 0;
        
        // Reload physics constants from config (in case difficulty changed)
        const difficultyMultiplier = config.getDifficultyMultiplier();
        this.gravity = config.SHIP_GRAVITY * difficultyMultiplier;
        this.thrustForce = config.SHIP_THRUST_FORCE * difficultyMultiplier;
        this.downThrustForce = config.SHIP_DOWN_THRUST_FORCE * difficultyMultiplier;
        this.drag = config.SHIP_DRAG_COEFFICIENT * difficultyMultiplier;
        this.terminalVelocityUp = config.SHIP_TERMINAL_VELOCITY_UP * difficultyMultiplier;
        this.terminalVelocityDown = config.SHIP_TERMINAL_VELOCITY_DOWN * difficultyMultiplier;
        
        // Reset visual state
        this.tilt = 0;
        this.targetTilt = 0;
        this.bobOffset = 0;
        this.time = 0;
        this.container.rotation = 0;
        this.sprite.y = 0;
        
        // Reset container visibility and alpha
        this.container.visible = true;
        this.container.alpha = 1.0;
        
        // Update visual effects
        this.updateVisuals();
        this.updateAmbientGlow();
        this.updateEngineGlow();
    }

    getBounds() {
        return {
            x: this.x - this.width/2,
            y: this.y - this.height/2,
            width: this.width,
            height: this.height
        };
    }
    
    // Method to get the PixiJS container for adding to stage
    getContainer() {
        return this.container;
    }
    
    // Method to destroy the ship
    destroy() {
        this.container.destroy();
    }
}

// Export the class for ES modules
export { PixiShip };
