class EventBus {
    constructor() {
        this.events = {};
    }
    
    on(event, callback) {
        if (!this.events[event]) {
            this.events[event] = [];
        }
        this.events[event].push(callback);
    }
    
    off(event, callback) {
        if (this.events[event]) {
            const index = this.events[event].indexOf(callback);
            if (index > -1) {
                this.events[event].splice(index, 1);
            }
        }
    }
    
    emit(event, data = null) {
        if (this.events[event]) {
            this.events[event].forEach(callback => {
                try {
                    callback(data);
                } catch (error) {
                    console.error(`Error in event handler for ${event}:`, error);
                }
            });
        }
    }
    
    once(event, callback) {
        const onceCallback = (data) => {
            callback(data);
            this.off(event, onceCallback);
        };
        this.on(event, onceCallback);
    }
    
    clear(event) {
        if (event) {
            delete this.events[event];
        } else {
            this.events = {};
        }
    }
}

// Game event constants
const GAME_EVENTS = {
    OBSTACLE_DESTROYED: 'obstacle_destroyed',
    AMMO_GAINED: 'ammo_gained',
    SCORE_INCREASED: 'score_increased',
    BULLET_FIRED: 'bullet_fired',
    SHIP_MOVED_UP: 'ship_moved_up',
    SHIP_MOVED_DOWN: 'ship_moved_down',
    GAME_STARTED: 'game_started',
    GAME_OVER: 'game_over',
    GAME_RESET: 'game_reset',
    GAME_PAUSED: 'game_paused',
    GAME_RESUMED: 'game_resumed',
    SETTINGS_CHANGED: 'settings_changed'
};

// Create global event bus instance
const eventBus = new EventBus();

// Export the class, instance, and constants for ES modules
export { EventBus, eventBus, GAME_EVENTS };
