// @ts-check
import { config } from './config/game.js';
import { rng } from './utils/rng.js';
import { eventBus, GAME_EVENTS } from './utils/eventBus.js';
import { Input } from './systems/input/Input.js';
import { gameState } from './core/GameState.js';
import { Ship } from './entities/Ship.js';
import { Obstacle } from './entities/Obstacle.js';
import { Bullet } from './entities/Bullet.js';
import { Renderer } from './systems/renderer/Renderer.js';
import { PixiGame } from './core/PixiGame.js';
import { Game } from './core/Game.js';
import { tweenManager } from './systems/tween/TweenManager.js';
import { particleSystem } from './systems/particles/ParticleSystem.js';
import { effectsManager } from './systems/effects/EffectsManager.js';
import { settingsManager } from './config/settings.js';

// Utility functions
function showErrorMessage(message) {
    const errorDiv = document.createElement('div');
    errorDiv.style.position = 'fixed';
    errorDiv.style.top = '50%';
    errorDiv.style.left = '50%';
    errorDiv.style.transform = 'translate(-50%, -50%)';
    errorDiv.style.padding = '20px';
    errorDiv.style.background = 'rgba(255, 0, 0, 0.8)';
    errorDiv.style.color = 'white';
    errorDiv.style.borderRadius = '5px';
    errorDiv.textContent = message;
    document.body.appendChild(errorDiv);
}

// Set up globals for backward compatibility
// @ts-ignore
window.config = config;
// @ts-ignore
window.rng = rng;
// @ts-ignore
window.eventBus = eventBus;
// @ts-ignore
window.GAME_EVENTS = GAME_EVENTS;
// @ts-ignore
window.gameState = gameState;
// @ts-ignore
window.tweenManager = tweenManager;
// @ts-ignore
window.particleSystem = particleSystem;
// @ts-ignore
window.effectsManager = effectsManager;
// @ts-ignore
window.settingsManager = settingsManager;

// Canvas setup
const canvas = /** @type {HTMLCanvasElement} */ (document.getElementById('gameCanvas'));
const ctx = canvas.getContext('2d');

// Declare game and renderer variables at the top
let game;
let startScreenRenderer;

// Canvas resizing function
function resizeCanvas() {
    // Get actual window dimensions
    const windowWidth = window.innerWidth;
    const windowHeight = window.innerHeight;
    
    // Limit canvas width to maximum 1200px
    const maxWidth = windowWidth;
    const canvasWidth = Math.min(windowWidth, maxWidth);
    const canvasHeight = windowHeight;
    
    // Set canvas to calculated size
    canvas.width = canvasWidth;
    canvas.height = canvasHeight;
    
    // Set CSS to match (no scaling)
    canvas.style.width = canvasWidth + 'px';
    canvas.style.height = canvasHeight + 'px';
    
    // If game exists, update its entities and renderer
    if (game) {
        // Update scaling and ship position (not size - ship stays fixed)
        game.updateScaling();
        if (game.ship) {
            game.ship.updatePosition();
            // Physics constants are now fixed values from config, no need to recalculate
        }
        
        // Resize renderer (only if game has resize method)
        if (game && typeof game.resize === 'function') {
            game.resize(canvasWidth, canvasHeight);
        }
    }
    
    // Update start screen renderer
    if (startScreenRenderer) {
        startScreenRenderer.stars = startScreenRenderer.createStars();
    }
}

// Setup resize listener
window.addEventListener('resize', resizeCanvas);
resizeCanvas();

// Initialize renderer for start screen background
startScreenRenderer = new Renderer(canvas, ctx);

// Animate start screen background
function animateStartScreen() {
    startScreenRenderer.clear();
    startScreenRenderer.drawStars();
    requestAnimationFrame(animateStartScreen);
}
animateStartScreen();

// Load ship image from external SVG file
const shipImage = new Image();
shipImage.src = '/icons/rocket.svg';

// Add error handling for image loading
shipImage.onerror = function() {
    console.error('Failed to load ship image:', shipImage.src);
    // Create a fallback canvas image
    const canvas = document.createElement('canvas');
    canvas.width = 40;
    canvas.height = 40;
    const ctx = canvas.getContext('2d');
    ctx.fillStyle = '#66CCFF';
    ctx.fillRect(0, 0, 40, 40);
    ctx.fillStyle = '#FFFFFF';
    ctx.fillRect(15, 10, 10, 20);
    shipImage.src = canvas.toDataURL();
};

// Initialize game when ship image loads (but don't start yet)
shipImage.onload = async function() {
    console.log('Ship image loaded successfully:', shipImage.src);
    
    // Load and initialize settings
    settingsManager.loadSettings();
    settingsManager.initializeUI(); // Make sure UI matches loaded settings
    
    try {
        // Try to initialize PixiJS game
        game = new PixiGame(canvas, ctx, gameState, shipImage);
        await game.initialize();
        console.log('PixiJS game initialized successfully');
        
        // Handle resize for PixiJS game
        window.addEventListener('resize', () => {
            resizeCanvas();
            game.handleResize && game.handleResize();
        });
    } catch (error) {
        console.warn('PixiJS initialization failed, falling back to Canvas 2D:', error);
        // Fallback to original Canvas 2D game
        game = new Game(canvas, ctx, gameState, shipImage);
    }
    
    // Apply settings to game and all systems
    settingsManager.applyAllSettings();
    
    // Update UI elements based on settings
    const fireButton = document.getElementById('fire-button');
    if (fireButton) {
        fireButton.style.display = settingsManager.get('autoFire') ? 'none' : 'flex';
    }
    
    const fpsCounter = document.getElementById('fps-counter');
    if (fpsCounter) {
        fpsCounter.classList.toggle('hidden', !settingsManager.get('fpsCounter'));
    }
    
    // Show start screen with current settings
    document.getElementById('start-screen').style.display = 'flex';
};

// Start button event listeners (both click and touch)
function startGame() {
    if (game) {
        // Resize canvas to current screen size before starting
        resizeCanvas();
        
        // Hide start screen
        document.getElementById('start-screen').style.display = 'none';
        
        // Save and apply current settings before starting
        settingsManager.saveSettings();
        settingsManager.applyAllSettings();
        
        // Show score display and game controls
        document.getElementById('score-display').style.display = 'block';
        document.getElementById('game-controls').style.display = 'flex';
        
        // Start the game
        game.start();
        
        // Add event listener for settings changes during gameplay
        eventBus.on(GAME_EVENTS.SETTINGS_CHANGED, () => {
            settingsManager.saveSettings();
        });
    }
}

// About screen navigation
function showAbout() {
    const startScreen = document.getElementById('start-screen');
    const aboutScreen = document.getElementById('about-screen');
    
    // Pause game if it's currently running
    if (game && game.gameActive) {
        game.pause();
    }
    
    startScreen.style.display = 'none';
    aboutScreen.style.display = 'flex';
}

function hideAbout() {
    const startScreen = document.getElementById('start-screen');
    const aboutScreen = document.getElementById('about-screen');
    
    aboutScreen.style.display = 'none';
    
    // Resume game if it was paused
    if (game && game.gameActive) {
        game.resume();
    }
    
    startScreen.style.display = 'flex';
}

// Settings screen navigation
function showSettings() {
    const startScreen = document.getElementById('start-screen');
    const settingsScreen = document.getElementById('settings-screen');
    
    // Pause game if it's currently running
    if (game && game.gameActive) {
        game.pause();
    }
    
    startScreen.style.display = 'none';
    settingsScreen.style.display = 'flex';
    
    // Initialize settings UI to ensure event listeners are properly set up
    settingsManager.initializeUI();
}

function hideSettings() {
    // Get screen elements
    const startScreen = document.getElementById('start-screen');
    const settingsScreen = document.getElementById('settings-screen');
    const gameSettingsOverlay = document.getElementById('game-settings-overlay');
    
    // Hide settings screens
    settingsScreen.style.display = 'none';
    if (gameSettingsOverlay) {
        gameSettingsOverlay.classList.remove('show');
    }
    
    // Save and apply any changed settings
    settingsManager.saveSettings();
    settingsManager.applyAllSettings();
    
    // Resume game if it was paused
    if (game && game.gameActive) {
        game.resume();
    }
    
    // Always show start screen when hiding settings (we're coming from main menu)
    startScreen.style.display = 'flex';
}
// Event listeners
const startButton = document.getElementById('start-button');
startButton.addEventListener('click', startGame);
startButton.addEventListener('touchend', (e) => {
    e.preventDefault(); // Prevent double-firing with click event
    startGame();
});

const aboutButton = document.getElementById('about-button');
aboutButton.addEventListener('click', showAbout);
aboutButton.addEventListener('touchend', (e) => {
    e.preventDefault();
    showAbout();
});

const settingsButton = document.getElementById('settings-button');
let settingsButtonTouchStarted = false;

settingsButton.addEventListener('click', (e) => {
    // Only handle click if touch didn't start
    if (!settingsButtonTouchStarted) {
        showSettings();
    }
    settingsButtonTouchStarted = false;
});

settingsButton.addEventListener('touchstart', (e) => {
    settingsButtonTouchStarted = true;
    e.stopPropagation();
    console.log('Settings button touchstart fired'); // Debug log
}, { passive: true });

settingsButton.addEventListener('touchend', (e) => {
    e.preventDefault();
    e.stopPropagation();
    console.log('Settings button touchend fired'); // Debug log
    showSettings();
    // Reset touch flag after a delay
    setTimeout(() => {
        settingsButtonTouchStarted = false;
    }, 100);
}, { passive: false });

const backFromAboutButton = document.getElementById('back-from-about-button');
backFromAboutButton.addEventListener('click', hideAbout);
backFromAboutButton.addEventListener('touchend', (e) => {
    e.preventDefault();
    hideAbout();
});

const backButton = document.getElementById('back-button');
backButton.addEventListener('click', hideSettings);
backButton.addEventListener('touchend', (e) => {
    e.preventDefault();
    hideSettings();
});

// Restart button event listeners (both click and touch)
function restartGame() {
    if (game) {
        // Stop the countdown timer since user is manually restarting
        if (game.stopCountdown) {
            game.stopCountdown();
        }
        
        // Hide game over screen
        const gameOverScreen = document.getElementById('game-over');
        gameOverScreen.style.display = 'none';
        
        // Reset the game
        game.reset();
        
        // Start a new game
        game.start();
        
        // Show game controls
        document.getElementById('game-controls').style.display = 'flex';
        
        // Show score display
        document.getElementById('score-display').style.display = 'block';
    }
}

const restartButton = document.getElementById('restart-button');
restartButton.addEventListener('click', restartGame);
restartButton.addEventListener('touchend', (e) => {
    e.preventDefault(); // Prevent double-firing with click event
    restartGame();
});

// Home button functionality
function goHome() {
    if (game) {
        // Stop the countdown timer since user is manually going home
        if (game.stopCountdown) {
            game.stopCountdown();
        }
        
        // Hide game over screen
        const gameOverScreen = document.getElementById('game-over');
        gameOverScreen.style.display = 'none';
        
        // Reset the game
        game.reset();
        
        // Show start screen
        const startScreen = document.getElementById('start-screen');
        startScreen.style.display = 'flex';
        
        // Hide game controls and score display
        document.getElementById('game-controls').style.display = 'none';
        document.getElementById('score-display').style.display = 'none';
    }
}

const homeButton = document.getElementById('home-button');
homeButton.addEventListener('click', goHome);
homeButton.addEventListener('touchend', (e) => {
    e.preventDefault(); // Prevent double-firing with click event
    goHome();
});

// Fire button event listeners (both mouse and touch)
const fireButton = document.getElementById('fire-button');

// Handle button press start
function startFiring(e) {
    e.preventDefault();
    if (game) {
        fireButton.classList.add('button-pressed');
        game.manualFire();
        // Start rapid fire while holding
        game.fireInterval = setInterval(() => {
            game.manualFire();
        }, config.BULLET_FIRE_INTERVAL);
    }
}

// Handle button press end
function stopFiring(e) {
    e.preventDefault();
    if (game) {
        fireButton.classList.remove('button-pressed');
        if (game.fireInterval) {
            clearInterval(game.fireInterval);
            game.fireInterval = null;
        }
    }
}

// Mouse events
fireButton.addEventListener('mousedown', startFiring);
fireButton.addEventListener('mouseup', stopFiring);
fireButton.addEventListener('mouseleave', stopFiring);

// Touch events
fireButton.addEventListener('touchstart', startFiring, { passive: false });
fireButton.addEventListener('touchend', stopFiring, { passive: false });
fireButton.addEventListener('touchcancel', stopFiring, { passive: false });

// Game settings functionality
function showGameSettings() {
    if (game && game.gameActive) {
        game.pause();
        const gameSettingsOverlay = document.getElementById('game-settings-overlay');
        gameSettingsOverlay.classList.add('show');
    }
}

function hideGameSettings() {
    const gameSettingsOverlay = document.getElementById('game-settings-overlay');
    gameSettingsOverlay.classList.remove('show');
    
    if (game && game.gameActive) {
        // Apply and save settings before resuming
        settingsManager.saveSettings();
        settingsManager.applyAllSettings();
        game.resume();
    }
}

// Game settings event listeners
const gameSettingsButton = document.getElementById('game-settings-button');
gameSettingsButton.addEventListener('click', showGameSettings);
gameSettingsButton.addEventListener('touchend', (e) => {
    e.preventDefault();
    showGameSettings();
});

const resumeGameButton = document.getElementById('resume-game-button');
resumeGameButton.addEventListener('click', hideGameSettings);
resumeGameButton.addEventListener('touchend', (e) => {
    e.preventDefault();
    hideGameSettings();
});
