// @ts-check
import { config } from '../config/game.js';

class Bullet {
    constructor(ship) {
        this.canvas = ship.canvas;
        this.x = ship.x + ship.width;
        this.y = ship.y + ship.height / 2;
        var bulletRadiusPercent = config.BULLET_RADIUS_PERCENT;
        if (this.canvas.width > 1000) {
            bulletRadiusPercent = 0.0050;
        }
        // Calculate size as percentage of canvas for full responsiveness
        this.width = Math.max(this.canvas.width * config.BULLET_WIDTH_PERCENT, 2);
        this.height = Math.max(this.canvas.height * config.BULLET_HEIGHT_PERCENT, 1);
    this.radius = Math.max(this.canvas.width * bulletRadiusPercent, 2);
        this.innerRadius = Math.max(this.canvas.width * config.BULLET_INNER_RADIUS_PERCENT, 1);
        
        // Calculate speed as percentage of canvas width for responsive behavior
        this.speed = config.getBulletSpeed(this.canvas.width);
    }

    update(deltaMultiplier = 1) {
        this.x += this.speed * deltaMultiplier;
    }

    draw(ctx) {
        // Outer glow
        ctx.shadowBlur = 15;
        ctx.shadowColor = '#66ccff';
        ctx.fillStyle = '#66ccff';
        ctx.beginPath();
        ctx.arc(this.x, this.y, this.radius, 0, Math.PI * 2);
        ctx.fill();

        // Bright center
        ctx.shadowBlur = 5;
        ctx.fillStyle = '#ffffff';
        ctx.beginPath();
        ctx.arc(this.x, this.y, this.innerRadius, 0, Math.PI * 2);
        ctx.fill();
        
        // Reset shadow
        ctx.shadowBlur = 0;
    }

    isOffScreen(canvas) {
        return this.x > (canvas || this.canvas).width;
    }

    reset(ship) {
        this.x = ship.x + ship.width;
        this.y = ship.y + ship.height / 2;
        this.canvas = ship.canvas;
        
        // Recalculate size and speed for new canvas
        this.width = Math.max(this.canvas.width * config.BULLET_WIDTH_PERCENT, 5);
        this.height = Math.max(this.canvas.height * config.BULLET_HEIGHT_PERCENT, 2);
        this.radius = Math.max(this.canvas.width * config.BULLET_RADIUS_PERCENT, 4);
        this.innerRadius = Math.max(this.canvas.width * config.BULLET_INNER_RADIUS_PERCENT, 2);
        this.speed = config.getBulletSpeed(this.canvas.width);
    }
}

// Export the class for ES modules
export { Bullet };


