// @ts-check
import { config } from '../config/game.js';
import { rng } from '../utils/rng.js';
import { eventBus, GAME_EVENTS } from '../utils/eventBus.js';
import { Input } from '../systems/input/Input.js';
import { Ship } from '../entities/Ship.js';
import { Obstacle } from '../entities/Obstacle.js';
import { Bullet } from '../entities/Bullet.js';
import { Renderer } from '../systems/renderer/Renderer.js';
import { tweenManager, Easing } from '../systems/tween/TweenManager.js';
import { settingsManager } from '../config/settings.js';
import { particleSystem } from '../systems/particles/ParticleSystem.js';
import { effectsManager } from '../systems/effects/EffectsManager.js';

class Game {
    constructor(canvas, ctx, gameState, shipImage) {
        this.canvas = canvas;
        this.ctx = ctx;
        this.gameState = gameState;
        this.shipImage = shipImage;
        
        // Game entities
        this.ship = new Ship(50, 150, shipImage, canvas);
        this.obstacles = [];
        this.bullets = [];
        
        // Game state
        this.frameCount = 0;
        this.gameActive = false; // Don't start active - wait for start button
        this.gamePaused = false; // Game pause state
        this.animationId = null;
        
        // Initialize settings from settingsManager
        this.autoFire = settingsManager.get('autoFire') ?? true;
        this.manualAim = settingsManager.get('manualAim') ?? false;
        this.fpsCounterEnabled = settingsManager.get('fpsCounter') ?? false;
        this.fps = 0;
        this.lastFpsUpdate = 0;
        this.fpsFrames = 0;
        
        // Apply initial UI state based on settings
        this.updateFireButtonVisibility();
        if (this.fpsCounterEnabled) {
            document.getElementById('fps-counter')?.classList.remove('hidden');
        }
        
        // Fixed timestep variables
        this.fixedTimeStep = config.FIXED_TIMESTEP;
        this.maxFrameSkip = config.MAX_FRAME_SKIP;
        this.accumulator = 0;
        this.lastTime = 0;
        this.interpolation = 0;
        
        // Internal resolution for consistent game feel
        this.internalWidth = config.INTERNAL_WIDTH;
        this.internalHeight = config.INTERNAL_HEIGHT;
        this.scaleX = 1;
        this.scaleY = 1;
        this.setupInternalCanvas();
        
        // Object pools
        this.bulletPool = [];
        this.obstaclePool = [];
        this.initializePools();
        
        // Input system
        this.input = new Input();
        
        // Renderer
        this.renderer = new Renderer(canvas, ctx, this.internalCanvas, this.internalCtx);
        
        // Setup
        this.setupControls();
        this.setupAutoShooting();
        this.setupEventListeners();
    }
    
    async initialize() {
        // Canvas-based game doesn't need async initialization
        return true;
    }

    setupInternalCanvas() {
        // Create internal canvas for consistent resolution
        this.internalCanvas = document.createElement('canvas');
        this.internalCanvas.width = this.internalWidth;
        this.internalCanvas.height = this.internalHeight;
        this.internalCtx = this.internalCanvas.getContext('2d');
        
        // Set up scaling
        this.updateScaling();
    }

    updateScaling() {
        const devicePixelRatio = window.devicePixelRatio || 1;
        this.scaleX = this.canvas.width / this.internalWidth;
        this.scaleY = this.canvas.height / this.internalHeight;
        
        // Update ship canvas reference to internal canvas
        this.ship.canvas = this.internalCanvas;
    }

    initializePools() {
        // Initialize bullet pool
        for (let i = 0; i < config.BULLET_POOL_SIZE; i++) {
            this.bulletPool.push(new Bullet(this.ship));
        }
        
        // Initialize obstacle pool
        for (let i = 0; i < config.OBSTACLE_POOL_SIZE; i++) {
            this.obstaclePool.push(new Obstacle(this.internalCanvas, this.gameState, 0));
        }
    }

    getPooledBullet() {
        for (let bullet of this.bulletPool) {
            if (bullet.isOffScreen(this.internalCanvas)) {
                bullet.reset(this.ship);
                return bullet;
            }
        }
        // If no available bullet, create new one
        return new Bullet(this.ship);
    }

    getPooledObstacle(frameCount) {
        for (let obstacle of this.obstaclePool) {
            if (obstacle.isOffScreen()) {
                obstacle.reset(this.internalCanvas, this.gameState, frameCount);
                return obstacle;
            }
        }
        // If no available obstacle, create new one
        return new Obstacle(this.internalCanvas, this.gameState, frameCount);
    }

    setupControls() {
        // Get button references
        this.upButton = document.getElementById('up-button');
        this.downButton = document.getElementById('down-button');
        this.fireButton = document.getElementById('fire-button');
        
        // Setup input system
        this.input.setupButtonEvents(this.upButton, this.downButton);
        this.input.setupCanvasEvents(this.canvas);
        
        // Connect input to ship movement
        this.input.on('onUp', () => {
            if (this.gameActive) {
                this.ship.applyLift();
                this.animateButton(this.upButton);
                eventBus.emit(GAME_EVENTS.SHIP_MOVED_UP);
            }
        });
        
        this.input.on('onDown', () => {
            if (this.gameActive) {
                this.ship.applyDownForce();
                this.animateButton(this.downButton);
                eventBus.emit(GAME_EVENTS.SHIP_MOVED_DOWN);
            }
        });
        
        // Handle manual firing with keyboard
        this.input.on('onFire', () => {
            if (this.gameActive && !this.autoFire) {
                if (this.manualFire()) {
                    this.animateButton(this.fireButton);
                }
            }
        });
        
        // Handle fire release
        this.input.on('onFireRelease', () => {
            if (this.gameActive && !this.autoFire) {
                if (this.fireInterval) {
                    clearInterval(this.fireInterval);
                    this.fireInterval = null;
                }
            }
        });
        
        // Handle thrust release for smooth physics
        this.input.on('onRelease', () => {
            if (this.gameActive) {
                this.ship.releaseThrust();
            }
        });
    }
    
    animateButton(button) {
        // Add active class for animation
        button.classList.add('button-pressed');
        
        // Remove the class after animation completes
        setTimeout(() => {
            button.classList.remove('button-pressed');
        }, 150);
    }
    
    animateAmmoGain(amount) {
        // Get the ammo display elements
        const ammoItem = document.querySelector('.score-item-nav:has(#bullet-count)');
        
        // Add animation class
        ammoItem.classList.add('ammo-gain');
        
        // Remove animation class after animation completes
        setTimeout(() => {
            ammoItem.classList.remove('ammo-gain');
        }, 600);
    }

    setupAutoShooting() {
        this.autoShootInterval = setInterval(() => {
            if (this.gameActive && this.autoFire && this.gameState.useBullet()) {
                const bullet = this.getPooledBullet();
                this.bullets.push(bullet);
                this.updateScoreDisplay();
                eventBus.emit(GAME_EVENTS.BULLET_FIRED, { bullet });
            }
        }, config.BULLET_FIRE_INTERVAL);
    }

    setupEventListeners() {
        // Listen for game events
        eventBus.on(GAME_EVENTS.OBSTACLE_DESTROYED, (data) => {
            this.gameState.currentScore++;
            this.updateScoreDisplay();
        });
        
        eventBus.on(GAME_EVENTS.AMMO_GAINED, (data) => {
            this.animateAmmoGain(data.amount);
        });
        
        eventBus.on(GAME_EVENTS.SCORE_INCREASED, (data) => {
            this.updateScoreDisplay();
        });
    }

    start() {
        // Update scaling and ship position
        this.updateScaling();
        this.ship.updatePosition();
        
        this.gameActive = true; // Activate the game
        this.lastTime = performance.now(); // Reset time for fixed timestep
        this.accumulator = 0;
        this.updateScoreDisplay();
        this.updateFireButtonVisibility(); // Set initial fire button visibility
        this.animate();
        
        eventBus.emit(GAME_EVENTS.GAME_STARTED);
    }

    reset() {
        // Save high score before reset
        this.gameState.resetGame();
        
        // Update scaling and ship position
        this.updateScaling();
        this.ship.updatePosition();
        
        // Reset game entities
        this.ship.reset();
        this.obstacles = [];
        this.bullets = [];
        this.frameCount = 0;
        this.gameActive = true;
        this.lastTime = performance.now(); // Reset time for fixed timestep
        this.accumulator = 0;
        
        // Reset input
        this.input.reset();
        
        // Reset renderer
        this.renderer.resetStars();
        
        // Update score display
        this.updateScoreDisplay();
        
        // Hide game over screen and show controls
        document.getElementById('game-over').style.display = 'none';
        document.getElementById('game-controls').style.display = 'flex';
        
        // Restart animation
        this.animate();
        
        eventBus.emit(GAME_EVENTS.GAME_RESET);
    }

    animate() {
        if (!this.gameActive || this.gamePaused) {
            return;
        }

        const currentTime = performance.now();
        let frameTime = currentTime - this.lastTime;
        this.lastTime = currentTime;

        // Prevent spiral of death
        if (frameTime > 250) {
            frameTime = 250;
        }

        this.accumulator += frameTime;

        // Fixed timestep updates
        while (this.accumulator >= this.fixedTimeStep) {
            this.fixedUpdate();
            this.accumulator -= this.fixedTimeStep;
        }

        // Calculate interpolation factor for smooth rendering
        this.interpolation = this.accumulator / this.fixedTimeStep;

        // Render with interpolation
        this.render();

        this.animationId = requestAnimationFrame(() => this.animate());
    }

    fixedUpdate() {
        // Update ship
        this.ship.update(1); // Fixed timestep, no delta multiplier needed

        // Check if ship is out of bounds
        if (this.ship.isOutOfBounds()) {
            this.gameOver();
            return;
        }

        // Spawn new obstacles using object pooling
        if (this.frameCount % 100 === 0) {
            const obstacle = this.getPooledObstacle(this.frameCount);
            this.obstacles.push(obstacle);
        }

        // Update and check obstacles
        if (!this.updateObstacles(1)) {
            return; // Game over
        }

        // Update and check bullets
        this.updateBullets(1);

        this.frameCount++;
    }

    render() {
        // Update effects
        effectsManager.update();
        tweenManager.update(performance.now());
        
        // Update FPS counter
        this.updateFps();

        // Clear internal canvas
        this.renderer.clear();
        this.renderer.drawStars();
        this.internalCtx.shadowBlur = 0;
        
        // Apply screen shake
        this.internalCtx.save();
        effectsManager.applyShake(this.internalCtx);
        
        // Draw ship with interpolation
        this.ship.draw(this.internalCtx);

        // Draw obstacles
        this.obstacles.forEach(obstacle => {
            obstacle.draw(this.internalCtx);
        });

        // Draw bullets
        this.bullets.forEach(bullet => {
            bullet.draw(this.internalCtx);
        });

        // Draw particles (minimal)
        particleSystem.update(1);
        particleSystem.draw(this.internalCtx);

        // Thrust particles based on ship's actual thrust
        const thrustIntensity = this.ship.getThrustIntensity();
        if (this.gameActive && thrustIntensity > 0.1 && Math.random() < 0.3) {
            particleSystem.thrust(
                this.ship.x, 
                this.ship.y + this.ship.height / 2, 
                thrustIntensity * 0.8
            );
        }

        this.internalCtx.restore();

        // Draw flash effect
        effectsManager.drawFlash(this.internalCtx, this.internalWidth, this.internalHeight);

        // Scale and draw to main canvas
        this.ctx.clearRect(0, 0, this.canvas.width, this.canvas.height);
        this.ctx.drawImage(
            this.internalCanvas,
            0, 0, this.internalWidth, this.internalHeight,
            0, 0, this.canvas.width, this.canvas.height
        );
        
        // Draw FPS counter on main canvas
        this.drawFpsCounter(this.ctx);
    }

    updateObstacles(deltaMultiplier) {
        for (let i = this.obstacles.length - 1; i >= 0; i--) {
            const obstacle = this.obstacles[i];
            
            obstacle.update(this.frameCount, deltaMultiplier);

            // Check collision with ship
            if (obstacle.checkCollision(this.ship)) {
                // Death effects - screen shake only on death
                particleSystem.damageSparks(
                    this.ship.x + this.ship.width / 2,
                    this.ship.y + this.ship.height / 2,
                    '#ff3333'
                );
                effectsManager.screenShake(12, 20);
                effectsManager.flash('#ff3333', 0.3);
                this.gameOver();
                return false;
            }

            // Check collision with enemy bullets
            if (obstacle.checkEnemyBulletCollision(this.ship)) {
                // Death effects - screen shake only on death
                particleSystem.damageSparks(
                    this.ship.x + this.ship.width / 2,
                    this.ship.y + this.ship.height / 2,
                    '#ff3333'
                );
                effectsManager.screenShake(12, 20);
                effectsManager.flash('#ff3333', 0.3);
                this.gameOver();
                return false;
            }

            // Check if ship passed the obstacle
            if (obstacle.x + obstacle.width < this.ship.x && !obstacle.passed) {
                obstacle.passed = true;
                const hits = this.gameState.getObstacleHits(obstacle.id);
                let bulletsAdded = 0;
                if (hits > 0) {
                    bulletsAdded = hits * config.BULLETS_PER_HIT;
                    this.gameState.addBullets(bulletsAdded);
                } else {
                    bulletsAdded = config.BULLETS_FOR_PASSING;
                    this.gameState.addBullets(bulletsAdded);
                }
                this.updateScoreDisplay();
                eventBus.emit(GAME_EVENTS.AMMO_GAINED, { amount: bulletsAdded });
            }

            // Remove obstacles that are off screen or fully destroyed
            if (obstacle.isOffScreen() || obstacle.isFullyDestroyed()) {
                this.obstacles.splice(i, 1);
            }
        }
        return true;
    }

    updateBullets(deltaMultiplier) {
        for (let i = this.bullets.length - 1; i >= 0; i--) {
            const bullet = this.bullets[i];
            
            bullet.update(deltaMultiplier);
            
            // Check collision with obstacles
            let bulletHit = false;
            for (let j = this.obstacles.length - 1; j >= 0; j--) {
                const obstacle = this.obstacles[j];
                
                if (obstacle.checkBulletCollision(bullet)) {
                    const hits = this.gameState.recordHit(obstacle.id);
                    this.bullets.splice(i, 1);
                    bulletHit = true;
                    
                    // Minimal visual feedback for hit
                    particleSystem.hitFlash(bullet.x, bullet.y, '#66ccff');
                    
                    if (hits >= config.HITS_TO_DESTROY) {
                        obstacle.startDestruction();
                        // Subtle destruction effects
                        particleSystem.explosion(
                            obstacle.x + obstacle.width / 2,
                            this.internalCanvas.height / 2,
                            '#9966ff',
                            15
                        );
                        eventBus.emit(GAME_EVENTS.OBSTACLE_DESTROYED, { obstacle, hits });
                    }
                    break;
                }
            }
            
            // Remove bullets that are off screen
            if (!bulletHit && bullet.isOffScreen(this.internalCanvas)) {
                this.bullets.splice(i, 1);
            }
        }
    }

    updateScoreDisplay() {
        document.getElementById('current-score').textContent = this.gameState.currentScore;
        document.getElementById('high-score').textContent = this.gameState.highScore;
        document.getElementById('bullet-count').textContent = this.gameState.bullets;
    }

    gameOver() {
        this.gameActive = false;
        
        // Save high score
        this.gameState.saveHighScore();
        
        // Hide game controls
        document.getElementById('game-controls').style.display = 'none';
        
        // Show game over screen
        const gameOverScreen = document.getElementById('game-over');
        document.getElementById('final-score').textContent = this.gameState.currentScore;
        document.getElementById('end-high-score').textContent = this.gameState.highScore;
        gameOverScreen.style.display = 'block';
        
        // Start countdown
        this.startCountdown();
        
        eventBus.emit(GAME_EVENTS.GAME_OVER, { 
            score: this.gameState.currentScore, 
            highScore: this.gameState.highScore 
        });
    }

    startCountdown() {
        let countdown = 5;
        const countdownElement = document.getElementById('countdown-timer');
        countdownElement.textContent = countdown.toString();

        const countdownInterval = setInterval(() => {
            countdown--;
            countdownElement.textContent = countdown.toString();

            if (countdown <= 0) {
                clearInterval(countdownInterval);
                window.location.reload();
            }
        }, 1000);
    }
    
    // Settings methods
    setAutoFire(enabled) {
        this.autoFire = enabled;
        this.updateFireButtonVisibility();
    }
    
    updateFireButtonVisibility() {
        const fireButton = document.getElementById('fire-button');
        if (fireButton) {
            fireButton.style.display = this.autoFire ? 'none' : 'flex';
        }
    }
    
    setManualAim(enabled) {
        this.manualAim = enabled;
        // TODO: Implement manual aiming system
    }
    
    manualFire() {
        // Fire a bullet manually if auto-fire is disabled and we have bullets
        if (!this.autoFire && this.gameActive && !this.gamePaused && this.gameState.useBullet()) {
            const bullet = this.getPooledBullet();
            this.bullets.push(bullet);
            this.updateScoreDisplay();
            eventBus.emit(GAME_EVENTS.BULLET_FIRED, { bullet });
            
            // Add visual feedback
            const fireButton = document.getElementById('fire-button');
            if (fireButton) {
                fireButton.classList.add('button-pressed');
                setTimeout(() => fireButton.classList.remove('button-pressed'), 100);
            }
            
            return true;
        }
        return false;
    }
    
    setFpsCounterEnabled(enabled) {
        this.fpsCounterEnabled = enabled;
    }
    
    updateFps() {
        if (!this.fpsCounterEnabled) return;
        
        this.fpsFrames++;
        const now = performance.now();
        
        if (now - this.lastFpsUpdate >= 1000) {
            this.fps = Math.round((this.fpsFrames * 1000) / (now - this.lastFpsUpdate));
            this.fpsFrames = 0;
            this.lastFpsUpdate = now;
        }
    }
    
    drawFpsCounter(ctx) {
        if (!this.fpsCounterEnabled) return;
        
        ctx.save();
        ctx.fillStyle = '#66ccff';
        ctx.font = '16px Orbitron, Exo 2, Arial, sans-serif';
        ctx.textAlign = 'left';
        ctx.fillText(`FPS: ${this.fps}`, 10, 30);
        ctx.restore();
    }
    
    pause() {
        this.gamePaused = true;
        if (this.animationId) {
            cancelAnimationFrame(this.animationId);
            this.animationId = null;
        }
        eventBus.emit(GAME_EVENTS.GAME_PAUSED);
    }
    
    resume() {
        // Re-sync settings before resuming
        this.autoFire = settingsManager.get('autoFire') ?? true;
        this.manualAim = settingsManager.get('manualAim') ?? false;
        this.fpsCounterEnabled = settingsManager.get('fpsCounter') ?? false;
        
        // Update UI based on current settings
        this.updateFireButtonVisibility();
        if (this.fpsCounterEnabled) {
            document.getElementById('fps-counter')?.classList.remove('hidden');
        } else {
            document.getElementById('fps-counter')?.classList.add('hidden');
        }
        
        // Resume game
        this.gamePaused = false;
        if (this.gameActive && !this.animationId) {
            this.lastTime = performance.now();
            this.animate();
        }
        eventBus.emit(GAME_EVENTS.GAME_RESUMED);
    }
    
    isPaused() {
        return this.gamePaused;
    }
    
    destroy() {
        // Clean up intervals
        if (this.autoShootInterval) {
            clearInterval(this.autoShootInterval);
        }
        
        // Clean up animation frame
        if (this.animationId) {
            cancelAnimationFrame(this.animationId);
        }
        
        // Clean up input
        if (this.input) {
            this.input.reset();
        }
        
        // Clean up renderer
        if (this.renderer) {
            this.renderer.resetStars();
        }
    }
}

// Export the class for ES modules
export { Game };
