// @ts-check

/**
 * @typedef {Object} ParticleOptions
 * @property {number} x
 * @property {number} y
 * @property {number} [vx]
 * @property {number} [vy]
 * @property {number} [life]
 * @property {number} [size]
 * @property {string} [color]
 * @property {number} [alpha]
 * @property {number} [gravity]
 * @property {number} [friction]
 * @property {number} [rotation]
 * @property {number} [rotationSpeed]
 * @property {string} [type]
 */

export class Particle {
  /**
   * @param {ParticleOptions} options
   */
  constructor(options) {
    this.reset(options);
  }

  /**
   * @param {ParticleOptions} options
   */
  reset(options) {
    this.x = options.x;
    this.y = options.y;
    this.vx = options.vx || 0;
    this.vy = options.vy || 0;
    this.life = options.life || 1;
    this.maxLife = this.life;
    this.size = options.size || 2;
    this.color = options.color || '#66ccff';
    this.alpha = options.alpha || 1;
    this.gravity = options.gravity || 0;
    this.friction = options.friction || 1;
    this.rotation = options.rotation || 0;
    this.rotationSpeed = options.rotationSpeed || 0;
    this.type = options.type || 'circle';
    this.isDead = false;
    this.trail = []; // For particle trails
    this.maxTrailLength = 3;
  }

  update(deltaMultiplier = 1) {
    // Store previous position for trail
    this.trail.push({ x: this.x, y: this.y });
    if (this.trail.length > this.maxTrailLength) {
      this.trail.shift();
    }
    
    // Apply physics
    this.x += this.vx * deltaMultiplier;
    this.y += this.vy * deltaMultiplier;
    this.vy += this.gravity * deltaMultiplier;
    
    // Apply friction (air resistance)
    this.vx *= this.friction;
    this.vy *= this.friction;
    
    // Apply rotation
    this.rotation += this.rotationSpeed * deltaMultiplier;
    
    // Decrease life
    this.life -= 0.016 * deltaMultiplier; // Roughly 60fps

    if (this.life <= 0) {
      this.isDead = true;
    }
  }

  draw(ctx) {
    if (this.isDead) return;

    const lifeRatio = this.life / this.maxLife;
    ctx.save();
    ctx.globalAlpha = this.alpha * lifeRatio;
    
    // Apply rotation
    ctx.translate(this.x, this.y);
    ctx.rotate(this.rotation);

    // Draw particle trail
    if (this.trail.length > 1) {
      ctx.strokeStyle = this.color;
      ctx.lineWidth = Math.max(0.5, this.size * lifeRatio * 0.3);
      ctx.globalAlpha = lifeRatio * 0.5;
      ctx.shadowBlur = 3;
      ctx.shadowColor = this.color;
      ctx.beginPath();
      ctx.moveTo(0, 0);
      for (let i = this.trail.length - 1; i >= 0; i--) {
        const trailPoint = this.trail[i];
        ctx.lineTo(trailPoint.x - this.x, trailPoint.y - this.y);
      }
      ctx.stroke();
    }

    // Reset alpha for main particle
    ctx.globalAlpha = this.alpha * lifeRatio;

    if (this.type === 'circle') {
      ctx.shadowBlur = 10;
      ctx.shadowColor = this.color;
      ctx.fillStyle = this.color;
      ctx.beginPath();
      ctx.arc(0, 0, this.size * lifeRatio, 0, Math.PI * 2);
      ctx.fill();
    } else if (this.type === 'star') {
      this.drawStar(ctx, 0, 0, 5, this.size * lifeRatio, this.size * lifeRatio * 0.5);
    } else if (this.type === 'line') {
      ctx.strokeStyle = this.color;
      ctx.lineWidth = this.size * lifeRatio;
      ctx.shadowBlur = 5;
      ctx.shadowColor = this.color;
      ctx.beginPath();
      ctx.moveTo(0, 0);
      ctx.lineTo(-this.vx * 2, -this.vy * 2);
      ctx.stroke();
    }

    ctx.restore();
  }

  drawStar(ctx, cx, cy, spikes, outerRadius, innerRadius) {
    let rot = (Math.PI / 2) * 3;
    let x = cx;
    let y = cy;
    const step = Math.PI / spikes;

    ctx.strokeStyle = this.color;
    ctx.fillStyle = this.color;
    ctx.shadowBlur = 15;
    ctx.shadowColor = this.color;
    ctx.beginPath();
    ctx.moveTo(cx, cy - outerRadius);

    for (let i = 0; i < spikes; i++) {
      x = cx + Math.cos(rot) * outerRadius;
      y = cy + Math.sin(rot) * outerRadius;
      ctx.lineTo(x, y);
      rot += step;

      x = cx + Math.cos(rot) * innerRadius;
      y = cy + Math.sin(rot) * innerRadius;
      ctx.lineTo(x, y);
      rot += step;
    }

    ctx.lineTo(cx, cy - outerRadius);
    ctx.closePath();
    ctx.fill();
  }
}
