// @ts-check
import * as PIXI from 'pixi.js';
import { config } from '../config/game.js';
import { rng } from '../utils/rng.js';
import { eventBus, GAME_EVENTS } from '../utils/eventBus.js';
import { PixiBullet } from '../entities/PixiBullet.js';
import { Input } from '../systems/input/Input.js';
import { PixiShip } from '../entities/PixiShip.js';
import { PixiObstacle } from '../entities/PixiObstacle.js';
import { PixiRenderer } from '../systems/renderer/PixiRenderer.js';
import { tweenManager, Easing } from '../systems/tween/TweenManager.js';
import { settingsManager } from '../config/settings.js';
import { particleSystem } from '../systems/particles/ParticleSystem.js';
import { effectsManager } from '../systems/effects/EffectsManager.js';
import { pixiParticleSystem } from '../systems/particles/PixiParticleSystem.js';
import { pixiEffectsManager } from '../systems/effects/PixiEffectsManager.js';
import { checkSystemRequirements } from '../utils/browserSupport';
import { Game } from './Game'; // Import original canvas-based game

class PixiGame {
    constructor(canvas, ctx, gameState, shipImage) {
        this.canvas = canvas;
        this.ctx = ctx;
        this.gameState = gameState;
        this.shipImage = shipImage;
        this.renderer = null;
        this.game = null;
        this.usePixi = false;
        this.isActive = false;
        this.fireInterval = null;
        
        // Store original canvas properties
        this.originalWidth = canvas.width;
        this.originalHeight = canvas.height;
    }

    async initialize() {
        const support = checkSystemRequirements();
        
        if (!support.canRunGame) {
            throw new Error('Browser does not meet minimum requirements to run the game');
        }

        try {
            if (support.canUseWebGL) {
                this.usePixi = true;
                await this.initializePixi();
                console.log('Using PixiJS renderer');
            } else {
                await this.initializeCanvas();
                console.log('Using Canvas 2D renderer');
            }
            return true;
        } catch (error) {
            console.error('Initialization error:', error);
            // If PixiJS fails, fallback to Canvas
            if (this.usePixi) {
                console.log('PixiJS initialization failed, falling back to Canvas');
                this.usePixi = false;
                await this.initializeCanvas();
                return true;
            }
            throw error;
        }
    }

    getSafariPixiConfig() {
        // Check if we're in Safari
        const ua = navigator.userAgent.toLowerCase();
        const isSafariBrowser = ua.includes('safari') && !ua.includes('chrome') && !ua.includes('chromium');
        const isIOSSafariBrowser = /iphone|ipad|ipod/.test(ua) && ua.includes('safari') && !ua.includes('chrome');
        const isMobileSafariBrowser = /iphone|ipad|ipod/.test(ua) && ua.includes('safari');

        if (!isSafariBrowser) {
            return {
                width: this.originalWidth,
                height: this.originalHeight,
                backgroundColor: 0x0a0a1a,
                resolution: window.devicePixelRatio || 1,
                autoDensity: true,
                antialias: true
            };
        }

        // Safari-specific configuration
        return {
            width: this.originalWidth,
            height: this.originalHeight,
            backgroundColor: 0x0a0a1a,
            resolution: window.devicePixelRatio || 1,
            autoDensity: true,
            antialias: isIOSSafariBrowser ? false : true, // Disable antialiasing on iOS Safari
            powerPreference: 'high-performance',
            preserveDrawingBuffer: isMobileSafariBrowser ? true : false,
            failIfMajorPerformanceCaveat: isMobileSafariBrowser ? false : true
        };
    }

    async initializePixi() {
        try {
            // Safari-specific configuration
            const safariConfig = this.getSafariPixiConfig();
            
            // Create application with Safari-compatible settings
            // @ts-ignore - Safari-specific configuration
            this.app = new PIXI.Application(safariConfig);

            // Initialize with basic settings first
            await this.app.init();

            // Configure renderer after initialization
            this.app.renderer.resize(this.originalWidth, this.originalHeight);
            this.app.renderer.background.color = 0x0a0a1a;
            
            // Replace canvas with PIXI's canvas
            this.canvas.parentNode.replaceChild(this.app.canvas, this.canvas);
            this.canvas = this.app.canvas;

            // Verify we have a working renderer
            if (!this.app.renderer) {
                this.app.destroy();
                throw new Error('Failed to initialize renderer');
            }

            // Create containers
            this.gameContainer = new PIXI.Container();
            this.backgroundContainer = new PIXI.Container();
            this.uiContainer = new PIXI.Container();
            this.effectsContainer = new PIXI.Container();

            this.app.stage.addChild(this.backgroundContainer);
            this.app.stage.addChild(this.gameContainer);
            this.app.stage.addChild(this.effectsContainer);
            this.app.stage.addChild(this.uiContainer);
            
            // Ensure background container is visible
            this.backgroundContainer.visible = true;
            this.backgroundContainer.alpha = 1.0;

            // Load assets
            await this.loadPixiAssets();

            // Setup game objects
            this.setupPixiGame();

        } catch (error) {
            console.error('PixiJS initialization failed:', error);
            throw new Error('PixiJS not supported - falling back to Canvas 2D');
        }
    }

    async initializeCanvas() {
        // Initialize original canvas-based game
        this.game = new Game(this.canvas, this.ctx, this.gameState, this.shipImage);
        return this.game.initialize();
    }

    async loadPixiAssets() {
        try {
            // Create a Sprite from the already loaded HTML Image
            const sprite = PIXI.Sprite.from(this.shipImage);
            this.shipTexture = sprite.texture;
            
            // Remove the temporary sprite
            sprite.destroy();
            
            return true;
        } catch (error) {
            console.error('Asset loading error:', error);
            throw new Error('Failed to load game assets: ' + error.message);
        }
    }

    setupPixiGame() {
        console.log('setupPixiGame called, usePixi:', this.usePixi);
        if (!this.usePixi) {
            console.log('setupPixiGame: usePixi is false, returning early');
            return;
        }
        
        // Initialize game state
        this.frameCount = 0;
        this.gameActive = false;
        this.gamePaused = false;
        this.animationId = null;
        
        // Initialize settings from settingsManager
        this.autoFire = settingsManager.get('autoFire') ?? true;
        this.manualAim = settingsManager.get('manualAim') ?? false;
        this.fpsCounterEnabled = settingsManager.get('fpsCounter') ?? false;
        this.fps = 0;
        this.lastFpsUpdate = 0;
        this.fpsFrames = 0;
        
        // Fixed timestep variables
        this.fixedTimeStep = config.FIXED_TIMESTEP;
        this.maxFrameSkip = config.MAX_FRAME_SKIP;
        this.accumulator = 0;
        this.lastTime = 0;
        this.interpolation = 0;
        
        // Initialize game entities
        console.log('Creating ship with texture:', this.shipTexture);
        console.log('App:', this.app);
        console.log('Original dimensions:', this.originalWidth, this.originalHeight);
        
        if (!this.shipTexture) {
            console.error('Ship texture not loaded!');
            return;
        }
        
        try {
            this.ship = new PixiShip(50, 150, this.shipTexture, this.app, this.originalWidth, this.originalHeight);
            console.log('Ship created successfully:', this.ship);
        } catch (error) {
            console.error('Failed to create ship:', error);
            throw error;
        }
        
        this.obstacles = [];
        this.bullets = [];
        
        // Add ship to the game container but keep it hidden initially
        if (this.ship) {
            this.gameContainer.addChild(this.ship.getContainer());
            // Hide ship initially - will be shown when game starts
            this.ship.getContainer().visible = false;
        } else {
            console.error('Cannot add ship to container - ship is null');
        }
        
        // Initialize object pools
        this.bulletPool = [];
        this.obstaclePool = [];
        this.initializePools();
        
            // Initialize stars
            this.createStars();
            
            // Add PIXI systems to stage
            this.effectsContainer.addChild(pixiParticleSystem.getContainer());
            this.effectsContainer.addChild(pixiEffectsManager.getFlashGraphics());
            
            // Input system
            this.input = new Input();
        
        // Setup
        this.setupControls();
        this.setupAutoShooting();
        this.setupEventListeners();
        
        // Make systems globally available for settings
        this.setupGlobalSystems();
    }

    initializePools() {
        // Initialize bullet pool
        for (let i = 0; i < config.BULLET_POOL_SIZE; i++) {
            this.bulletPool.push(new PixiBullet(this.ship, this.app, this.originalWidth, this.originalHeight));
        }
        
        // Initialize obstacle pool
        for (let i = 0; i < config.OBSTACLE_POOL_SIZE; i++) {
            this.obstaclePool.push(new PixiObstacle(this.app, this.gameState, 0, this.originalWidth, this.originalHeight));
        }
    }

    createStars() {
        this.stars = [];
        this.starSprites = [];
        
        for (let i = 0; i < config.STAR_COUNT; i++) {
            const star = {
                x: rng.random() * this.originalWidth,
                y: rng.random() * this.originalHeight,
                size: Math.max(rng.randomFloat(config.STAR_MIN_SIZE, config.STAR_MAX_SIZE), 1),
                speed: rng.randomFloat(config.STAR_MIN_SPEED, config.STAR_MAX_SPEED),
                color: this.getStarColor()
            };
            this.stars.push(star);
            
            // Create PIXI Graphics for each star
            const starSprite = new PIXI.Graphics();
            starSprite.beginFill(star.color);
            starSprite.drawRect(-star.size/2, -star.size/2, star.size, star.size);
            starSprite.endFill();
            starSprite.x = star.x;
            starSprite.y = star.y;
            starSprite.alpha = 1.0;
            
            this.backgroundContainer.addChild(starSprite);
            this.starSprites.push(starSprite);
        }
    }

    getStarColor() {
        const colors = [0xFFFFFF, 0x88DDFF, 0xAA88FF, 0xFF88DD, 0xFFFF88, 0x88FF88];
        return rng.randomChoice(colors);
    }

    drawStars() {
        if (!this.usePixi) return;
        
        // Update star positions
        this.stars.forEach((star, index) => {
            star.x -= star.speed;
            if (star.x < 0) {
                star.x = this.originalWidth + star.size;
                star.y = rng.random() * this.originalHeight;
            }
            
            // Update sprite position
            this.starSprites[index].x = star.x;
            this.starSprites[index].y = star.y;
        });
    }

    setupControls() {
        // Get button references
        this.upButton = document.getElementById('up-button');
        this.downButton = document.getElementById('down-button');
        this.fireButton = document.getElementById('fire-button');
        
        // Setup input system
        this.input.setupButtonEvents(this.upButton, this.downButton);
        this.input.setupCanvasEvents(this.canvas);
        
        // Connect input to ship movement
        this.input.on('onUp', () => {
            if (this.gameActive) {
                this.ship.applyLift();
                this.animateButton(this.upButton);
                eventBus.emit(GAME_EVENTS.SHIP_MOVED_UP);
            }
        });
        
        this.input.on('onDown', () => {
            if (this.gameActive) {
                this.ship.applyDownForce();
                this.animateButton(this.downButton);
                eventBus.emit(GAME_EVENTS.SHIP_MOVED_DOWN);
            }
        });
        
        // Handle manual firing with keyboard
        this.input.on('onFire', () => {
            if (this.gameActive && !this.autoFire) {
                if (this.manualFire()) {
                    this.animateButton(this.fireButton);
                }
            }
        });
        
        // Handle fire release
        this.input.on('onFireRelease', () => {
            if (this.gameActive && !this.autoFire) {
                if (this.fireInterval) {
                    clearInterval(this.fireInterval);
                    this.fireInterval = null;
                }
            }
        });
        
        // Handle thrust release for smooth physics
        this.input.on('onRelease', () => {
            if (this.gameActive) {
                this.ship.releaseThrust();
            }
        });
    }

    setupAutoShooting() {
        this.autoShootInterval = setInterval(() => {
            if (this.gameActive && this.autoFire && this.gameState.useBullet()) {
                const bullet = this.getPooledBullet();
                this.bullets.push(bullet);
                // Add bullet to the game container
                this.gameContainer.addChild(bullet.getContainer());
                this.updateScoreDisplay();
                
                // Emit event with properly positioned bullet
                eventBus.emit(GAME_EVENTS.BULLET_FIRED, { bullet });
            }
        }, config.BULLET_FIRE_INTERVAL);
    }

    setupEventListeners() {
        // Listen for game events
        eventBus.on(GAME_EVENTS.OBSTACLE_DESTROYED, (data) => {
            this.gameState.currentScore++;
            this.updateScoreDisplay();
            
            // Trigger explosion effect
            if (data.obstacle) {
                const centerX = data.obstacle.x + data.obstacle.width / 2;
                const centerY = data.obstacle.appHeight / 2;
                if (this.usePixi) {
                    pixiParticleSystem.explosion(centerX, centerY, 0x9966ff, 25);
                    pixiEffectsManager.screenShake(3, 8);
                    pixiEffectsManager.flash(0x9966ff, 0.3);
                } else {
                    particleSystem.explosion(centerX, centerY, '#9966ff', 25);
                    effectsManager.screenShake(3, 8);
                    effectsManager.flash('#9966ff', 0.3);
                }
            }
        });
        
        eventBus.on(GAME_EVENTS.AMMO_GAINED, (data) => {
            this.animateAmmoGain(data.amount);
        });
        
        eventBus.on(GAME_EVENTS.SCORE_INCREASED, (data) => {
            this.updateScoreDisplay();
        });
        
        eventBus.on(GAME_EVENTS.BULLET_FIRED, (data) => {
            // Temporarily disable bullet trail effects to prevent artifacts
            // TODO: Re-enable once particle cleanup is fully fixed
            /*
            if (data.bullet && settingsManager.get('particles')) {
                if (this.usePixi) {
                    pixiParticleSystem.bulletTrail(data.bullet.x, data.bullet.y);
                } else {
                    particleSystem.bulletTrail(data.bullet.x, data.bullet.y);
                }
            }
            */
        });
        
        eventBus.on(GAME_EVENTS.SHIP_MOVED_UP, () => {
            // Add thrust particles
            if (this.ship) {
                const thrustX = this.ship.x - this.ship.width / 2;
                const thrustY = this.ship.y;
                if (this.usePixi) {
                    pixiParticleSystem.thrust(thrustX, thrustY, this.ship.getThrustIntensity());
                } else {
                    particleSystem.thrust(thrustX, thrustY, this.ship.getThrustIntensity());
                }
            }
        });
        
        eventBus.on(GAME_EVENTS.SHIP_MOVED_DOWN, () => {
            // Add thrust particles
            if (this.ship) {
                const thrustX = this.ship.x - this.ship.width / 2;
                const thrustY = this.ship.y;
                if (this.usePixi) {
                    pixiParticleSystem.thrust(thrustX, thrustY, this.ship.getThrustIntensity());
                } else {
                    particleSystem.thrust(thrustX, thrustY, this.ship.getThrustIntensity());
                }
            }
        });
    }
    
    setupGlobalSystems() {
        // Make systems globally available for settings manager
        window.game = this;
        
        // Use PIXI systems for PIXI games, Canvas systems for Canvas games
        if (this.usePixi) {
            window.effectsManager = pixiEffectsManager;
            window.particleSystem = pixiParticleSystem;
        } else {
            window.effectsManager = effectsManager;
            window.particleSystem = particleSystem;
        }
        
        window.tweenManager = tweenManager;
        
        // Apply current settings
        settingsManager.applyAllSettings();
    }

    getPooledBullet() {
        for (let bullet of this.bulletPool) {
            if (bullet.isOffScreen()) {
                bullet.reset(this.ship, this.originalWidth, this.originalHeight);
                return bullet;
            }
        }
        // If no available bullet, create new one
        return new PixiBullet(this.ship, this.app, this.originalWidth, this.originalHeight);
    }

    getPooledObstacle(frameCount) {
        for (let obstacle of this.obstaclePool) {
            if (obstacle.isOffScreen()) {
                obstacle.reset(this.app, this.gameState, frameCount, this.originalWidth, this.originalHeight);
                return obstacle;
            }
        }
        // If no available obstacle, create new one
        return new PixiObstacle(this.app, this.gameState, frameCount, this.originalWidth, this.originalHeight);
    }

    animateButton(button) {
        // Add active class for animation
        button.classList.add('button-pressed');
        
        // Remove the class after animation completes
        setTimeout(() => {
            button.classList.remove('button-pressed');
        }, 150);
    }

    animateAmmoGain(amount) {
        // Get the ammo display elements
        const ammoItem = document.querySelector('.score-item-nav:has(#bullet-count)');
        
        // Add animation class
        ammoItem.classList.add('ammo-gain');
        
        // Remove animation class after animation completes
        setTimeout(() => {
            ammoItem.classList.remove('ammo-gain');
        }, 600);
    }

    updateScoreDisplay() {
        document.getElementById('current-score').textContent = this.gameState.currentScore;
        document.getElementById('high-score').textContent = this.gameState.highScore;
        document.getElementById('bullet-count').textContent = this.gameState.bullets;
    }

    update(deltaTime) {
        if (this.usePixi) {
            if (!this.gameActive || this.gamePaused) return;

            // Update ship
            if (this.ship) {
                this.ship.update(deltaTime);

                // Check if ship is out of bounds
                if (this.ship.isOutOfBounds()) {
                    this.gameOver();
                    return;
                }
            }

            // Spawn new obstacles using object pooling
            if (this.frameCount % 100 === 0) {
                const obstacle = this.getPooledObstacle(this.frameCount);
                this.obstacles.push(obstacle);
                // Add obstacle to the game container
                this.gameContainer.addChild(obstacle.getContainer());
            }

            // Update and check obstacles
            if (!this.updateObstacles(deltaTime)) {
                return; // Game over
            }

            // Update and check bullets
            this.updateBullets(deltaTime);

            // Update stars
            this.drawStars();
            
            // Update systems
            this.updateSystems(deltaTime);

            this.frameCount++;

        } else {
            // Original canvas update logic
            if (this.game) {
                this.game.animate();
            }
        }
    }

    updateSystems(deltaMultiplier) {
        if (!this.usePixi) return;
        
        // Update particle system
        pixiParticleSystem.update(deltaMultiplier);
        
        // Update effects manager
        pixiEffectsManager.update();
        
        // Apply screen shake to game container
        pixiEffectsManager.applyShake(this.gameContainer);
        
        // Update flash effect
        pixiEffectsManager.drawFlash(this.originalWidth, this.originalHeight);
        
        // Update tween manager
        tweenManager.update(performance.now());
    }

    updateObstacles(deltaMultiplier) {
        for (let i = this.obstacles.length - 1; i >= 0; i--) {
            const obstacle = this.obstacles[i];
            
            obstacle.update(this.frameCount, deltaMultiplier);

            // Check collision with ship
            if (this.ship && obstacle.checkCollision(this.ship)) {
                // Add damage effect
                if (this.usePixi) {
                    pixiParticleSystem.damageSparks(this.ship.x, this.ship.y, 0xff3333);
                    pixiEffectsManager.screenShake(8, 15);
                    pixiEffectsManager.flash(0xff3333, 0.6);
                } else {
                    particleSystem.damageSparks(this.ship.x, this.ship.y, '#ff3333');
                    effectsManager.screenShake(8, 15);
                    effectsManager.flash('#ff3333', 0.6);
                }
                this.gameOver();
                return false;
            }

            // Check collision with enemy bullets
            if (this.ship && obstacle.checkEnemyBulletCollision(this.ship)) {
                // Add damage effect
                if (this.usePixi) {
                    pixiParticleSystem.damageSparks(this.ship.x, this.ship.y, 0xff3333);
                    pixiEffectsManager.screenShake(8, 15);
                    pixiEffectsManager.flash(0xff3333, 0.6);
                } else {
                    particleSystem.damageSparks(this.ship.x, this.ship.y, '#ff3333');
                    effectsManager.screenShake(8, 15);
                    effectsManager.flash('#ff3333', 0.6);
                }
                this.gameOver();
                return false;
            }

            // Check if ship passed the obstacle
            if (this.ship && obstacle.x + obstacle.width < this.ship.x && !obstacle.passed) {
                obstacle.passed = true;
                const hits = this.gameState.getObstacleHits(obstacle.id);
                let bulletsAdded = 0;
                if (hits > 0) {
                    bulletsAdded = hits * config.BULLETS_PER_HIT;
                    this.gameState.addBullets(bulletsAdded);
                } else {
                    bulletsAdded = config.BULLETS_FOR_PASSING;
                    this.gameState.addBullets(bulletsAdded);
                }
                this.updateScoreDisplay();
                eventBus.emit(GAME_EVENTS.AMMO_GAINED, { amount: bulletsAdded });
            }

            // Remove obstacles that are off screen or fully destroyed
            if (obstacle.isOffScreen() || obstacle.isFullyDestroyed()) {
                // Remove from PixiJS stage
                if (obstacle.getContainer && this.gameContainer.children.includes(obstacle.getContainer())) {
                    this.gameContainer.removeChild(obstacle.getContainer());
                }
                this.obstacles.splice(i, 1);
            }
        }
        return true;
    }

    updateBullets(deltaMultiplier) {
        for (let i = this.bullets.length - 1; i >= 0; i--) {
            const bullet = this.bullets[i];
            
            bullet.update(deltaMultiplier);
            
            // Check collision with obstacles
            let bulletHit = false;
            for (let j = this.obstacles.length - 1; j >= 0; j--) {
                const obstacle = this.obstacles[j];
                
                if (obstacle.checkBulletCollision(bullet)) {
                    const hits = this.gameState.recordHit(obstacle.id);
                    this.bullets.splice(i, 1);
                    // Remove from PixiJS stage and ensure complete cleanup
                    this.cleanupBullet(bullet);
                    bulletHit = true;
                    
                    // Add realistic hit sparks effect with bullet direction
                    if (this.usePixi) {
                        // Set bullet direction for directional sparks
                        pixiParticleSystem.setBulletDirection({ x: bullet.vx, y: bullet.vy });
                        pixiParticleSystem.hitSparks(bullet.x, bullet.y, 0x66ccff);
                        pixiEffectsManager.hitStop(2);
                    } else {
                        // Set bullet direction for directional sparks
                        particleSystem.setBulletDirection({ x: bullet.vx, y: bullet.vy });
                        particleSystem.hitSparks(bullet.x, bullet.y, '#66ccff');
                        effectsManager.hitStop(2);
                    }
                    
                    if (hits >= config.HITS_TO_DESTROY) {
                        obstacle.startDestruction();
                        eventBus.emit(GAME_EVENTS.OBSTACLE_DESTROYED, { obstacle, hits });
                    }
                    break;
                }
            }
            
            // Remove bullets that are off screen
            if (!bulletHit && bullet.isOffScreen()) {
                this.bullets.splice(i, 1);
                // Remove from PixiJS stage and ensure complete cleanup
                this.cleanupBullet(bullet);
            }
        }
    }
    
    /**
     * Properly cleanup a bullet to prevent visual artifacts
     * @param {PixiBullet} bullet - The bullet to cleanup
     */
    cleanupBullet(bullet) {
        if (!bullet) return;
        
        try {
            // Remove from game container
            if (bullet.getContainer && this.gameContainer.children.includes(bullet.getContainer())) {
                this.gameContainer.removeChild(bullet.getContainer());
            }
            
            // Clear graphics to prevent visual artifacts
            if (bullet.graphics) {
                bullet.graphics.clear();
                bullet.graphics.destroy();
            }
            
            // Destroy container
            if (bullet.container) {
                bullet.container.destroy({ children: true });
            }
            
            // Reset bullet for pool reuse
            bullet.container = null;
            bullet.graphics = null;
        } catch (error) {
            console.warn('Error cleaning up bullet:', error);
        }
    }

    manualFire() {
        // Fire a bullet manually if auto-fire is disabled and we have bullets
        if (!this.autoFire && this.gameActive && !this.gamePaused && this.gameState.useBullet()) {
            const bullet = this.getPooledBullet();
            this.bullets.push(bullet);
            // Add bullet to the game container
            this.gameContainer.addChild(bullet.getContainer());
            this.updateScoreDisplay();
            
            // Emit event with properly positioned bullet
            eventBus.emit(GAME_EVENTS.BULLET_FIRED, { bullet });
            
            // Add visual feedback
            const fireButton = document.getElementById('fire-button');
            if (fireButton) {
                fireButton.classList.add('button-pressed');
                setTimeout(() => fireButton.classList.remove('button-pressed'), 100);
            }
            
            return true;
        }
        return false;
    }

    gameOver() {
        this.gameActive = false;
        
        // Hide ship when game ends
        if (this.ship) {
            this.ship.getContainer().visible = false;
        }
        
        // Save high score
        this.gameState.saveHighScore();
        
        // Hide game controls
        document.getElementById('game-controls').style.display = 'none';
        
        // Show game over screen
        const gameOverScreen = document.getElementById('game-over');
        document.getElementById('final-score').textContent = this.gameState.currentScore;
        document.getElementById('end-high-score').textContent = this.gameState.highScore;
        gameOverScreen.style.display = 'block';
        
        // Start countdown
        this.startCountdown();
        
        eventBus.emit(GAME_EVENTS.GAME_OVER, { 
            score: this.gameState.currentScore, 
            highScore: this.gameState.highScore 
        });
    }

    startCountdown() {
        let countdown = 5;
        const countdownElement = document.getElementById('countdown-timer');
        countdownElement.textContent = countdown.toString();

        // Store the interval so it can be cleared if user clicks buttons
        this.countdownInterval = setInterval(() => {
            countdown--;
            countdownElement.textContent = countdown.toString();

            if (countdown <= 0) {
                clearInterval(this.countdownInterval);
                this.countdownInterval = null;
                // Only restart automatically if no user interaction
                this.restartGame();
            }
        }, 1000);
    }
    
    stopCountdown() {
        if (this.countdownInterval) {
            clearInterval(this.countdownInterval);
            this.countdownInterval = null;
        }
    }
    
    restartGame() {
        // Stop countdown timer if it's running
        this.stopCountdown();
        
        // Hide game over screen
        const gameOverScreen = document.getElementById('game-over');
        gameOverScreen.style.display = 'none';
        
        // Reset the game
        this.reset();
        
        // Start a new game
        this.start();
        
        // Show game controls
        document.getElementById('game-controls').style.display = 'flex';
        
        // Show score display
        document.getElementById('score-display').style.display = 'block';
    }

    pause() {
        if (this.usePixi) {
            this.gamePaused = true;
            // Stop animation loop
            if (this.animationId) {
                cancelAnimationFrame(this.animationId);
                this.animationId = null;
            }
        } else {
            this.isActive = false;
            if (this.fireInterval) {
                clearInterval(this.fireInterval);
                this.fireInterval = null;
            }
            if (this.game && typeof this.game.pause === 'function') {
                this.game.pause();
            }
        }
    }

    resume() {
        if (this.usePixi) {
            this.gamePaused = false;
            // Resume animation loop if game is active
            if (this.gameActive) {
                this.animate();
            }
        } else {
            this.isActive = true;
            if (this.game && typeof this.game.resume === 'function') {
                this.game.resume();
            }
        }
    }

    reset() {
        if (this.usePixi) {
            // Save high score before reset
            this.gameState.reset();
            
            // Stop current animation
            if (this.animationId) {
                cancelAnimationFrame(this.animationId);
                this.animationId = null;
            }
            
            // Clean up all game objects from containers
            this.cleanupGameObjects();
            
            // Reset game state
            this.frameCount = 0;
            this.gameActive = false; // Will be set to true in start()
            this.gamePaused = false;
            this.lastTime = performance.now();
            this.accumulator = 0;
            
            // Reset ship
            if (this.ship) {
                this.ship.reset();
                this.ship.updatePosition();
                // Hide ship during reset - will be shown when new game starts
                this.ship.getContainer().visible = false;
            }
            
            // Clear arrays
            this.obstacles = [];
            this.bullets = [];
            
            // Reset input
            if (this.input) {
                this.input.reset();
            }
            
            // Reset stars
            this.clear();
            
            // Clear PIXI systems with aggressive cleanup
            if (this.usePixi) {
                pixiParticleSystem.clear();
                pixiEffectsManager.reset();
                // Force clear any remaining graphics in effects container
                this.effectsContainer.children.forEach(child => {
                    if (child && child.destroy) {
                        child.destroy({ children: true });
                    }
                });
                this.effectsContainer.removeChildren();
            }
            
            // Update scaling
            this.updateScaling();
            
            // Update score display
            this.updateScoreDisplay();
            
            // Hide game over screen and show controls
            document.getElementById('game-over').style.display = 'none';
            document.getElementById('game-controls').style.display = 'flex';
            
            eventBus.emit(GAME_EVENTS.GAME_RESET);
        } else if (this.game && typeof this.game.reset === 'function') {
            this.game.reset();
        }
    }

    render() {
        if (this.usePixi) {
            // PIXI will handle rendering automatically
        } else {
            // Original canvas render logic
            if (this.game) {
                this.game.render();
            }
        }
    }

    updateScaling() {
        if (this.usePixi) {
            // Get the current canvas size
            const width = this.canvas.width;
            const height = this.canvas.height;
            
            // Update renderer size
            this.app.renderer.resize(width, height);
            
            // Scale the game container to maintain aspect ratio
            const scale = Math.min(width / this.originalWidth, height / this.originalHeight);
            this.gameContainer.scale.set(scale);
            
            // Center the game container
            this.gameContainer.x = (width - this.originalWidth * scale) / 2;
            this.gameContainer.y = (height - this.originalHeight * scale) / 2;
            
            // Update background and UI containers
            this.backgroundContainer.width = width;
            this.backgroundContainer.height = height;
            this.uiContainer.width = width;
            this.uiContainer.height = height;
        } else if (this.game && typeof this.game.updateScaling === 'function') {
            this.game.updateScaling();
        }
    }

    handleResize() {
        this.updateScaling();
    }


    start() {
        if (this.usePixi) {
            // Update scaling and ship position
            this.updateScaling();
            if (this.ship) {
                this.ship.updatePosition();
                // Make ship visible when game starts
                this.ship.getContainer().visible = true;
            }
            
            this.gameActive = true; // Activate the game
            this.lastTime = performance.now(); // Reset time for fixed timestep
            this.accumulator = 0;
            this.updateScoreDisplay();
            this.updateFireButtonVisibility(); // Set initial fire button visibility
            this.animate();
            
            eventBus.emit(GAME_EVENTS.GAME_STARTED);

        } else if (this.game) {
            // Delegate to canvas game implementation
            this.game.start();
        }
    }

    animate() {
        if (!this.gameActive || this.gamePaused) {
            return;
        }

        const currentTime = performance.now();
        let frameTime = currentTime - this.lastTime;
        this.lastTime = currentTime;

        // Prevent spiral of death
        if (frameTime > 250) {
            frameTime = 250;
        }

        this.accumulator += frameTime;

        // Fixed timestep updates
        while (this.accumulator >= this.fixedTimeStep) {
            this.fixedUpdate();
            this.accumulator -= this.fixedTimeStep;
        }

        // Calculate interpolation factor for smooth rendering
        this.interpolation = this.accumulator / this.fixedTimeStep;

        // Render with interpolation
        this.render();

        this.animationId = requestAnimationFrame(() => this.animate());
    }

    fixedUpdate() {
        if (this.usePixi) {
            // Update ship
            if (this.ship) {
                this.ship.update(1); // Fixed timestep, no delta multiplier needed

                // Check if ship is out of bounds
                if (this.ship.isOutOfBounds()) {
                    this.gameOver();
                    return;
                }
            }

            // Spawn new obstacles using object pooling
            if (this.frameCount % 100 === 0) {
                const obstacle = this.getPooledObstacle(this.frameCount);
                this.obstacles.push(obstacle);
                // Add obstacle to the game container
                this.gameContainer.addChild(obstacle.getContainer());
            }

            // Update and check obstacles
            if (!this.updateObstacles(1)) {
                return; // Game over
            }

            // Update and check bullets
            this.updateBullets(1);

            // Update stars
            this.drawStars();

            this.frameCount++;
        }
    }


    cleanupGameObjects() {
        if (!this.usePixi) return;
        
        // Remove all obstacles from game container
        this.obstacles.forEach(obstacle => {
            if (obstacle && obstacle.getContainer && this.gameContainer.children.includes(obstacle.getContainer())) {
                this.gameContainer.removeChild(obstacle.getContainer());
            }
        });
        
        // Remove all bullets from game container with proper cleanup
        this.bullets.forEach(bullet => {
            this.cleanupBullet(bullet);
        });
        
        // Reset object pools with proper cleanup
        this.bulletPool.forEach(bullet => {
            if (bullet && bullet.getContainer && this.gameContainer.children.includes(bullet.getContainer())) {
                this.gameContainer.removeChild(bullet.getContainer());
            }
            // Ensure graphics are cleared
            if (bullet && bullet.graphics) {
                bullet.graphics.clear();
            }
        });
        
        this.obstaclePool.forEach(obstacle => {
            if (obstacle && obstacle.getContainer && this.gameContainer.children.includes(obstacle.getContainer())) {
                this.gameContainer.removeChild(obstacle.getContainer());
            }
        });
        
        // Clear arrays
        this.obstacles = [];
        this.bullets = [];
        
        // Clear particle system to remove any lingering effects
        if (this.usePixi) {
            pixiParticleSystem.clear();
            // Force clear any remaining graphics in effects container
            this.effectsContainer.children.forEach(child => {
                if (child && child.destroy) {
                    child.destroy({ children: true });
                }
            });
            this.effectsContainer.removeChildren();
        }
    }

    clear() {
        if (this.usePixi) {
            // Clear the background (but keep stars)
            // Only clear non-star children if any exist
            const childrenToRemove = this.backgroundContainer.children.filter(child => 
                !this.starSprites.includes(child)
            );
            childrenToRemove.forEach(child => this.backgroundContainer.removeChild(child));
        }
    }

    updateFps() {
        if (!this.fpsCounterEnabled) return;
        
        this.fpsFrames++;
        const now = performance.now();
        
        if (now - this.lastFpsUpdate >= 1000) {
            this.fps = Math.round((this.fpsFrames * 1000) / (now - this.lastFpsUpdate));
            this.fpsFrames = 0;
            this.lastFpsUpdate = now;
        }
    }

    drawFpsCounter() {
        if (!this.fpsCounterEnabled) return;
        
        // Create FPS text if it doesn't exist
        if (!this.fpsText) {
            this.fpsText = new PIXI.Text(`FPS: ${this.fps}`, {
                fontFamily: 'Orbitron, Exo 2, Arial, sans-serif',
                fontSize: 16,
                fill: 0x66ccff,
                align: 'left'
            });
            this.fpsText.x = 10;
            this.fpsText.y = 10;
            this.uiContainer.addChild(this.fpsText);
        }
        
        this.fpsText.text = `FPS: ${this.fps}`;
    }

    updateFireButtonVisibility() {
        const fireButton = document.getElementById('fire-button');
        if (fireButton) {
            fireButton.style.display = this.autoFire ? 'none' : 'flex';
        }
    }
    
    setFpsCounterEnabled(enabled) {
        this.fpsCounterEnabled = enabled;
        if (!enabled && this.fpsText) {
            this.uiContainer.removeChild(this.fpsText);
            this.fpsText = null;
        }
    }
    
    setAutoFire(enabled) {
        this.autoFire = enabled;
        this.updateFireButtonVisibility();
    }
    
    setManualAim(enabled) {
        this.manualAim = enabled;
        // TODO: Implement manual aiming system for PIXI
    }

    setupInput() {
        // Setup keyboard controls
        window.addEventListener('keydown', (e) => {
            if (!this.isActive) return;
            switch (e.key) {
                case 'ArrowUp':
                case 'w':
                    if (this.ship) this.ship.targetThrust = this.ship.thrustForce;
                    break;
                case 'ArrowDown':
                case 's':
                    if (this.ship) this.ship.targetThrust = -this.ship.downThrustForce;
                    break;
                case ' ':
                    // Fire bullet implementation will be added later
                    break;
            }
        });

        window.addEventListener('keyup', (e) => {
            if (!this.isActive) return;
            switch (e.key) {
                case 'ArrowUp':
                case 'w':
                case 'ArrowDown':
                case 's':
                    if (this.ship) this.ship.targetThrust = 0;
                    break;
            }
        });

        // Touch controls will be added here
    }

    cleanup() {
        try {
            if (this.usePixi) {
                if (this.app) {
                    this.app.destroy(true, { children: true, texture: true });
                    this.app = null;
                }
                if (this.shipTexture) {
                    this.shipTexture.destroy(true);
                    this.shipTexture = null;
                }
            }
            
            if (this.game && typeof this.game.destroy === 'function') {
                this.game.destroy();
                this.game = null;
            }
        } catch (error) {
            console.error('Error during cleanup:', error);
        }
    }
}

// Export the class for ES modules
export { PixiGame };
