class SeededRNG {
    constructor(seed = null) {
        this.seed = seed || Date.now();
        this.current = this.seed;
    }
    
    // Linear Congruential Generator (LCG)
    next() {
        this.current = (this.current * 1664525 + 1013904223) % Math.pow(2, 32);
        return this.current / Math.pow(2, 32);
    }
    
    // Generate random number between 0 and 1
    random() {
        return this.next();
    }
    
    // Generate random integer between min (inclusive) and max (exclusive)
    randomInt(min, max) {
        return Math.floor(this.random() * (max - min)) + min;
    }
    
    // Generate random float between min and max
    randomFloat(min, max) {
        return this.random() * (max - min) + min;
    }
    
    // Generate random boolean
    randomBool() {
        return this.random() < 0.5;
    }
    
    // Generate random element from array
    randomChoice(array) {
        if (array.length === 0) {
            throw new Error('Cannot choose from empty array');
        }
        return array[this.randomInt(0, array.length)];
    }
    
    // Shuffle array in place
    shuffle(array) {
        for (let i = array.length - 1; i > 0; i--) {
            const j = this.randomInt(0, i + 1);
            [array[i], array[j]] = [array[j], array[i]];
        }
        return array;
    }
    
    // Reset to initial seed
    reset() {
        this.current = this.seed;
    }
    
    // Set new seed
    setSeed(seed) {
        this.seed = seed;
        this.current = seed;
    }
    
    // Get current state (for saving/loading)
    getState() {
        return {
            seed: this.seed,
            current: this.current
        };
    }
    
    // Restore state (for loading)
    setState(state) {
        this.seed = state.seed;
        this.current = state.current;
    }
}

// Create global RNG instance
const rng = new SeededRNG();

// Export the class and instance for ES modules
export { SeededRNG, rng };
