class Input {
    constructor() {
        this.keys = {
            up: false,
            down: false,
            fire: false
        };
        
        this.touches = {
            up: false,
            down: false,
            fire: false
        };
        
        this.callbacks = {
            onUp: [],
            onDown: [],
            onUpRelease: [],
            onDownRelease: [],
            onFire: [],
            onFireRelease: [],
            onRelease: []
        };
        
        this.setupKeyboardEvents();
        this.setupTouchEvents();
    }
    
    setupKeyboardEvents() {
        window.addEventListener('keydown', (e) => {
            if (e.code === 'ArrowUp' && !this.keys.up) {
                this.keys.up = true;
                this.triggerCallbacks('onUp');
            } else if (e.code === 'ArrowDown' && !this.keys.down) {
                this.keys.down = true;
                this.triggerCallbacks('onDown');
            } else if ((e.code === 'Enter' || e.code === 'Space') && !this.keys.fire) {
                e.preventDefault(); // Prevent page scroll on space
                this.keys.fire = true;
                this.triggerCallbacks('onFire');
            }
        });
        
        window.addEventListener('keyup', (e) => {
            if (e.code === 'ArrowUp') {
                this.keys.up = false;
                this.triggerCallbacks('onUpRelease');
                this.triggerCallbacks('onRelease');
            } else if (e.code === 'ArrowDown') {
                this.keys.down = false;
                this.triggerCallbacks('onDownRelease');
                this.triggerCallbacks('onRelease');
            } else if (e.code === 'Enter' || e.code === 'Space') {
                this.keys.fire = false;
                this.triggerCallbacks('onFireRelease');
                this.triggerCallbacks('onRelease');
            }
        });
    }
    
    setupTouchEvents() {
        // Prevent default touch behavior
        document.addEventListener('touchstart', (e) => {
            e.preventDefault();
        }, { passive: false });
        
        document.addEventListener('touchmove', (e) => {
            e.preventDefault();
        }, { passive: false });
    }
    
    setupButtonEvents(upButton, downButton) {
        // Up button events
        upButton.addEventListener('mousedown', () => {
            if (!this.touches.up) {
                this.touches.up = true;
                this.triggerCallbacks('onUp');
            }
        });
        
        upButton.addEventListener('touchstart', (e) => {
            e.preventDefault();
            if (!this.touches.up) {
                this.touches.up = true;
                this.triggerCallbacks('onUp');
            }
        }, { passive: false });
        
        upButton.addEventListener('mouseup', () => {
            this.touches.up = false;
            this.triggerCallbacks('onUpRelease');
            this.triggerCallbacks('onRelease');
        });
        
        upButton.addEventListener('touchend', (e) => {
            e.preventDefault();
            this.touches.up = false;
            this.triggerCallbacks('onUpRelease');
            this.triggerCallbacks('onRelease');
        }, { passive: false });
        
        // Down button events
        downButton.addEventListener('mousedown', () => {
            if (!this.touches.down) {
                this.touches.down = true;
                this.triggerCallbacks('onDown');
            }
        });
        
        downButton.addEventListener('touchstart', (e) => {
            e.preventDefault();
            if (!this.touches.down) {
                this.touches.down = true;
                this.triggerCallbacks('onDown');
            }
        }, { passive: false });
        
        downButton.addEventListener('mouseup', () => {
            this.touches.down = false;
            this.triggerCallbacks('onDownRelease');
            this.triggerCallbacks('onRelease');
        });
        
        downButton.addEventListener('touchend', (e) => {
            e.preventDefault();
            this.touches.down = false;
            this.triggerCallbacks('onDownRelease');
            this.triggerCallbacks('onRelease');
        }, { passive: false });
    }
    
    setupCanvasEvents(canvas) {
        // Canvas tap for UP movement
        canvas.addEventListener('mousedown', () => {
            if (!this.touches.up) {
                this.touches.up = true;
                this.triggerCallbacks('onUp');
            }
        });
        
        canvas.addEventListener('touchstart', (e) => {
            e.preventDefault();
            if (!this.touches.up) {
                this.touches.up = true;
                this.triggerCallbacks('onUp');
            }
        }, { passive: false });
        
        canvas.addEventListener('mouseup', () => {
            this.touches.up = false;
            this.triggerCallbacks('onUpRelease');
            this.triggerCallbacks('onRelease');
        });
        
        canvas.addEventListener('touchend', (e) => {
            e.preventDefault();
            this.touches.up = false;
            this.triggerCallbacks('onUpRelease');
            this.triggerCallbacks('onRelease');
        }, { passive: false });
    }
    
    triggerCallbacks(action) {
        if (this.callbacks[action]) {
            this.callbacks[action].forEach(callback => callback());
        }
    }
    
    on(action, callback) {
        if (this.callbacks[action]) {
            this.callbacks[action].push(callback);
        }
    }
    
    off(action, callback) {
        if (this.callbacks[action]) {
            const index = this.callbacks[action].indexOf(callback);
            if (index > -1) {
                this.callbacks[action].splice(index, 1);
            }
        }
    }
    
    isUpPressed() {
        return this.keys.up || this.touches.up;
    }
    
    isDownPressed() {
        return this.keys.down || this.touches.down;
    }
    
    reset() {
        this.keys.up = false;
        this.keys.down = false;
        this.keys.fire = false;
        this.touches.up = false;
        this.touches.down = false;
        this.touches.fire = false;
    }

    isFirePressed() {
        return this.keys.fire || this.touches.fire;
    }
}

// Export the class for ES modules
export { Input };
