// @ts-check

/**
 * @typedef {Object} DifficultyScaling
 * @property {number} EASY
 * @property {number} NORMAL
 * @property {number} HARD
 * @property {number} INSANE
 */

/**
 * @typedef {'EASY' | 'NORMAL' | 'HARD' | 'INSANE'} DifficultyLevel
 */

class Config {
    constructor() {
        // Game timing
        this.FIXED_TIMESTEP = 1000 / 60; // 60 FPS in milliseconds
        this.MAX_FRAME_SKIP = 5;
        
        // Internal resolution (16:9 aspect ratio)
        this.INTERNAL_WIDTH = 900;   // Base width for internal rendering
        this.INTERNAL_HEIGHT = 1600; // Base height for internal rendering
        
        // Ship physics (improved physics model)
        this.SHIP_GRAVITY = 0.3; // Constant downward pull (pixels per frame squared)
        this.SHIP_THRUST_FORCE = 2.0; // Upward thrust strength
        this.SHIP_DOWN_THRUST_FORCE = 1.5; // Downward thrust strength
        this.SHIP_DRAG_COEFFICIENT = 0.02; // Air resistance (velocity squared)
        this.SHIP_TERMINAL_VELOCITY_UP = -8; // Max upward speed
        this.SHIP_TERMINAL_VELOCITY_DOWN = 10; // Max downward speed
        this.SHIP_THRUST_SMOOTHING = 0.3; // How fast thrust responds (0-1)
        this.SHIP_TILT_FACTOR = 3; // How much velocity affects tilt
        this.SHIP_TILT_SMOOTHING = 0.2; // Tilt interpolation speed
        this.SHIP_BOB_SPEED = 0.05; // Idle bob animation speed
        this.SHIP_BOB_AMPLITUDE = 2; // Idle bob amplitude in pixels
        
        // Legacy physics (kept for compatibility)
        this.SHIP_LIFT_FORCE = 0.0067; // Percentage of canvas height
        this.SHIP_DOWN_FORCE = 0.0067; // Percentage of canvas height
        this.SHIP_VELOCITY_INCREMENT = 0.0002; // Percentage of canvas height
        this.SHIP_INITIAL_VELOCITY = 0.000167; // Percentage of canvas height
        
        // Ship dimensions (percentages of canvas)
        this.SHIP_WIDTH_PERCENT = 0.125; // 12.5% of canvas width
        this.SHIP_HEIGHT_PERCENT = 0.05; // 5% of canvas height
        this.SHIP_X_PERCENT = 0.125; // 12.5% from left
        this.SHIP_Y_PERCENT = 0.25; // 25% from top
        
        // Bullet system
        this.BULLET_WIDTH_PERCENT = 0.0025; // 0.25% of canvas width
        this.BULLET_HEIGHT_PERCENT = 0.0025; // 0.25% of canvas height
        this.BULLET_RADIUS_PERCENT = 0.01; // 1% of canvas width
        this.BULLET_INNER_RADIUS_PERCENT = 0.0025; // 0.25% of canvas width
        this.BULLET_SPEED_PERCENT = 0.01; // 1% of canvas width
        this.BULLET_FIRE_INTERVAL = 100; // milliseconds
        this.BULLET_POOL_SIZE = 50;
        
        // Obstacle system
        this.OBSTACLE_SPAWN_INTERVAL = 100; // frames
        this.OBSTACLE_WIDTH_PERCENT = 0.035; // 7.5% of canvas width
        this.OBSTACLE_MIN_WIDTH = 30; // pixels
        this.OBSTACLE_SPEED_PERCENT = 0.003; // 0.3% of canvas width
        this.OBSTACLE_BULLET_SPEED_PERCENT = 0.005; // 0.5% of canvas width
        this.OBSTACLE_GUN_CHANCE = 0.4; // 40% chance
        this.OBSTACLE_SHOOT_INTERVAL = 120; // frames
        this.OBSTACLE_POOL_SIZE = 20;
        
        // Obstacle dimensions (percentages of canvas height)
        this.OBSTACLE_MIN_TOP_HEIGHT = 0.083; // 8.3%
        this.OBSTACLE_TOP_VARIATION = 0.333; // 33.3%
        this.OBSTACLE_GAP_SIZE = 0.333; // 33.3%
        
        // Game mechanics
        this.HITS_TO_DESTROY = 5;
        this.BULLETS_PER_HIT = 4;
        this.BULLETS_FOR_PASSING = 20;
        this.INITIAL_BULLETS = 100;
        this.RESET_BULLETS = 20;
        this.MAX_BULLETS = 100;
        
        // Visual effects
        this.STAR_COUNT = 75;
        this.STAR_MIN_SIZE = 2;
        this.STAR_MAX_SIZE = 6;
        this.STAR_MIN_SPEED = 0.2;
        this.STAR_MAX_SPEED = 0.7;
        
        // Particle effects
        this.PARTICLE_COUNT = 15;
        this.PARTICLE_RANGE_PERCENT = 0.167; // 16.7% of canvas height
        this.PARTICLE_BASE_SPEED_PERCENT = 0.00083; // Base speed relative to canvas height
        this.PARTICLE_MAX_SPEED_VARIATION_PERCENT = 0.0025; // Speed variation
        this.PARTICLE_MIN_SIZE_PERCENT = 0.0025; // Min particle size
        this.PARTICLE_MAX_SIZE_PERCENT = 0.0075; // Max particle size
        
        // UI
        this.BUTTON_ANIMATION_DURATION = 150;
        this.AMMO_GAIN_ANIMATION_DURATION = 600;
        this.COUNTDOWN_DURATION = 5000; // 5 seconds
        
        // Difficulty scaling
        this.DIFFICULTY_SCALING = {
            EASY: 1.0,
            NORMAL: 1.2,
            HARD: 1.5,
            INSANE: 2.0
        };
        
        this.currentDifficulty = 'NORMAL';
    }
    
    /**
     * @returns {number}
     */
    getDifficultyMultiplier() {
        return this.DIFFICULTY_SCALING[this.currentDifficulty] || 1.0;
    }
    
    /**
     * @param {DifficultyLevel} difficulty
     */
    setDifficulty(difficulty) {
        if (this.DIFFICULTY_SCALING[difficulty]) {
            this.currentDifficulty = difficulty;
        }
    }
    
    // Get scaled values based on canvas dimensions
    /**
     * @param {number} canvasHeight
     * @returns {number}
     */
    getShipLift(canvasHeight) {
        return -canvasHeight * this.SHIP_LIFT_FORCE * this.getDifficultyMultiplier();
    }
    
    /**
     * @param {number} canvasHeight
     * @returns {number}
     */
    getShipDownForce(canvasHeight) {
        return canvasHeight * this.SHIP_DOWN_FORCE * this.getDifficultyMultiplier();
    }
    
    /**
     * @param {number} canvasHeight
     * @returns {number}
     */
    getShipVelocityIncrement(canvasHeight) {
        return canvasHeight * this.SHIP_VELOCITY_INCREMENT * this.getDifficultyMultiplier();
    }
    
    /**
     * @param {number} canvasHeight
     * @returns {number}
     */
    getShipInitialVelocity(canvasHeight) {
        return canvasHeight * this.SHIP_INITIAL_VELOCITY * this.getDifficultyMultiplier();
    }
    
    /**
     * @param {number} canvasWidth
     * @returns {number}
     */
    getBulletSpeed(canvasWidth) {
        return canvasWidth * this.BULLET_SPEED_PERCENT * this.getDifficultyMultiplier();
    }
    
    /**
     * @param {number} canvasWidth
     * @returns {number}
     */
    getObstacleSpeed(canvasWidth) {
        return canvasWidth * this.OBSTACLE_SPEED_PERCENT * this.getDifficultyMultiplier();
    }
    
    /**
     * @param {number} canvasWidth
     * @returns {number}
     */
    getObstacleBulletSpeed(canvasWidth) {
        return canvasWidth * this.OBSTACLE_BULLET_SPEED_PERCENT * this.getDifficultyMultiplier();
    }
}

// Create global config instance
const config = new Config();

// Export the class and instance for ES modules
export { Config, config };
