// @ts-check
import { config } from './game.js';

/**
 * Settings Manager - Handles all game settings and preferences
 */
class SettingsManager {
    constructor() {
        this.settings = {
            // Difficulty
            difficulty: 'NORMAL',
            
            // Gameplay
            autoFire: true,
            manualAim: false,
            screenShake: true,
            
            // Visual
            particles: true,
            engineGlow: true,
            fpsCounter: false,
            
            // Audio
            masterVolume: 80,
            music: true,
            sfx: true
        };
        
        this.loadSettings();
        this.setupEventListeners();
        
        // Note: Settings will be applied after game is created in main.js
    }
    
    /**
     * Load settings from localStorage
     */
    loadSettings() {
        const saved = localStorage.getItem('cosmicHighwaySettings');
        if (saved) {
            try {
                const parsed = JSON.parse(saved);
                this.settings = { ...this.settings, ...parsed };
            } catch (e) {
                console.warn('Failed to load settings:', e);
            }
        }
        
        // Update UI with loaded settings
        this.updateUI();
    }
    
    /**
     * Save settings to localStorage
     */
    saveSettings() {
        try {
            localStorage.setItem('cosmicHighwaySettings', JSON.stringify(this.settings));
        } catch (e) {
            console.warn('Failed to save settings:', e);
        }
    }
    
    /**
     * Get a setting value
     * @param {string} key - Setting key
     * @returns {any} Setting value
     */
    get(key) {
        return this.settings[key];
    }
    
    /**
     * Set a setting value
     * @param {string} key - Setting key
     * @param {any} value - Setting value
     */
    set(key, value) {
        this.settings[key] = value;
        this.saveSettings();
        this.applySetting(key, value);
        
        // Emit settings changed event
        if (window.eventBus) {
            window.eventBus.emit(window.GAME_EVENTS.SETTINGS_CHANGED, { key, value });
        }
    }
    
    /**
     * Apply all settings to the game systems
     */
    applyAllSettings() {
        Object.keys(this.settings).forEach(key => {
            this.applySetting(key, this.settings[key]);
        });
    }
    
    /**
     * Apply a setting to the game
     * @param {string} key - Setting key
     * @param {any} value - Setting value
     */
    applySetting(key, value) {
        switch (key) {
            case 'difficulty':
                config.setDifficulty(value);
                // Update ship physics if game is running
                if (window.game && window.game.ship) {
                    window.game.ship.updatePhysicsForDifficulty();
                }
                break;
            case 'autoFire':
                this.updateAutoFire(value);
                break;
            case 'manualAim':
                this.updateManualAim(value);
                break;
            case 'screenShake':
                this.updateScreenShake(value);
                break;
            case 'particles':
                this.updateParticles(value);
                break;
            case 'engineGlow':
                this.updateEngineGlow(value);
                break;
            case 'fpsCounter':
                this.updateFpsCounter(value);
                break;
            case 'masterVolume':
                this.updateMasterVolume(value);
                break;
            case 'music':
                this.updateMusic(value);
                break;
            case 'sfx':
                this.updateSfx(value);
                break;
        }
    }
    
    /**
     * Update auto-fire setting
     * @param {boolean} enabled - Whether auto-fire is enabled
     */
    updateAutoFire(enabled) {
        // This will be connected to the game's auto-shooting system
        if (window.game) {
            window.game.setAutoFire(enabled);
        }
    }
    
    /**
     * Update manual aim setting
     * @param {boolean} enabled - Whether manual aim is enabled
     */
    updateManualAim(enabled) {
        // This will be connected to the game's shooting system
        if (window.game) {
            window.game.setManualAim(enabled);
        }
        
        // Show notification that manual aim is not yet implemented
        if (enabled) {
            this.showNotification('Manual aim feature coming soon!');
        }
    }
    
    /**
     * Update screen shake setting
     * @param {boolean} enabled - Whether screen shake is enabled
     */
    updateScreenShake(enabled) {
        if (window.effectsManager) {
            window.effectsManager.setScreenShakeEnabled(enabled);
        }
    }
    
    /**
     * Update particles setting
     * @param {boolean} enabled - Whether particles are enabled
     */
    updateParticles(enabled) {
        if (window.particleSystem) {
            window.particleSystem.setEnabled(enabled);
        }
    }
    
    /**
     * Update engine glow setting
     * @param {boolean} enabled - Whether engine glow is enabled
     */
    updateEngineGlow(enabled) {
        if (window.game && window.game.ship) {
            window.game.ship.setEngineGlowEnabled(enabled);
        }
    }
    
    /**
     * Update FPS counter setting
     * @param {boolean} enabled - Whether FPS counter is enabled
     */
    updateFpsCounter(enabled) {
        if (window.game) {
            window.game.setFpsCounterEnabled(enabled);
        }
    }
    
    /**
     * Update master volume setting
     * @param {number} volume - Volume level (0-100)
     */
    updateMasterVolume(volume) {
        // This will be connected to the audio system
        if (window.audioManager) {
            window.audioManager.setMasterVolume(volume / 100);
        }
    }
    
    /**
     * Update music setting
     * @param {boolean} enabled - Whether music is enabled
     */
    updateMusic(enabled) {
        if (window.audioManager) {
            window.audioManager.setMusicEnabled(enabled);
        }
    }
    
    /**
     * Update SFX setting
     * @param {boolean} enabled - Whether SFX are enabled
     */
    updateSfx(enabled) {
        if (window.audioManager) {
            window.audioManager.setSfxEnabled(enabled);
        }
    }
    
    /**
     * Setup event listeners for settings UI
     */
    setupEventListeners() {
        // Wait for DOM to be ready
        if (document.readyState === 'loading') {
            document.addEventListener('DOMContentLoaded', () => this.initializeUI());
        } else {
            this.initializeUI();
        }
    }
    
    /**
     * Convert toggle ID to setting key
     * @param {string} toggleId - The toggle checkbox ID
     * @returns {string} The corresponding setting key
     */
    getSettingKeyFromToggleId(toggleId) {
        const idToKeyMap = {
            'auto-fire-toggle': 'autoFire',
            'manual-aim-toggle': 'manualAim',
            'screen-shake-toggle': 'screenShake',
            'particles-toggle': 'particles',
            'engine-glow-toggle': 'engineGlow',
            'fps-toggle': 'fpsCounter',
            'music-toggle': 'music',
            'sfx-toggle': 'sfx',
            // Game settings overlay toggles
            'game-auto-fire-toggle': 'autoFire',
            'game-manual-aim-toggle': 'manualAim',
            'game-screen-shake-toggle': 'screenShake',
            'game-particles-toggle': 'particles',
            'game-engine-glow-toggle': 'engineGlow',
            'game-fps-toggle': 'fpsCounter',
            'game-music-toggle': 'music',
            'game-sfx-toggle': 'sfx'
        };
        return idToKeyMap[toggleId] || toggleId.replace('-toggle', '');
    }
    
    /**
     * Remove existing event listeners to prevent duplicates
     */
    removeEventListeners() {
        // Clone elements to remove all event listeners
        const difficultyButtons = document.querySelectorAll('.difficulty-btn');
        difficultyButtons.forEach(btn => {
            const newBtn = btn.cloneNode(true);
            btn.parentNode.replaceChild(newBtn, btn);
        });
        
        const toggles = document.querySelectorAll('input[type="checkbox"]');
        toggles.forEach(toggle => {
            const newToggle = toggle.cloneNode(true);
            toggle.parentNode.replaceChild(newToggle, toggle);
        });
        
        const volumeSliders = document.querySelectorAll('input[type="range"]');
        volumeSliders.forEach(slider => {
            const newSlider = slider.cloneNode(true);
            slider.parentNode.replaceChild(newSlider, slider);
        });
        
        const resetButton = document.getElementById('reset-settings');
        if (resetButton) {
            const newResetButton = resetButton.cloneNode(true);
            resetButton.parentNode.replaceChild(newResetButton, resetButton);
        }
    }
    
    /**
     * Initialize the settings UI
     */
    initializeUI() {
        // Remove existing event listeners to prevent duplicates
        this.removeEventListeners();
        // Difficulty buttons (both main settings and game settings)
        const difficultyButtons = document.querySelectorAll('.difficulty-btn');
        difficultyButtons.forEach(btn => {
            const button = /** @type {HTMLButtonElement} */ (btn);
            // Handle click events
            button.addEventListener('click', () => {
                const difficulty = button.dataset.difficulty;
                this.setDifficulty(difficulty);
            });
            
            // Handle touch events for better mobile interaction
            button.addEventListener('touchstart', (e) => {
                e.stopPropagation();
            }, { passive: true });
            
            button.addEventListener('touchend', (e) => {
                e.preventDefault();
                const difficulty = button.dataset.difficulty;
                this.setDifficulty(difficulty);
            }, { passive: false });
            
            // Improve accessibility
            button.setAttribute('aria-label', `Set difficulty to ${button.dataset.difficulty}`);
            button.setAttribute('role', 'button');
        });
        
        // Toggle switches
        const toggles = document.querySelectorAll('input[type="checkbox"]');
        toggles.forEach(toggle => {
            const checkbox = /** @type {HTMLInputElement} */ (toggle);
            const toggleSwitch = checkbox.closest('.toggle-switch');
            
            // Handle change events
            checkbox.addEventListener('change', () => {
                const key = this.getSettingKeyFromToggleId(checkbox.id);
                this.set(key, checkbox.checked);
            });
            
            // Handle touch events on the toggle switch container for better mobile interaction
            if (toggleSwitch) {
                toggleSwitch.addEventListener('touchstart', (e) => {
                    e.stopPropagation();
                    console.log('Toggle switch touchstart fired for:', checkbox.id); // Debug log
                }, { passive: true });
                
                toggleSwitch.addEventListener('touchend', (e) => {
                    e.preventDefault();
                    e.stopPropagation();
                    console.log('Toggle switch touchend fired for:', checkbox.id); // Debug log
                    // Toggle the checkbox manually for touch devices
                    checkbox.checked = !checkbox.checked;
                    const key = this.getSettingKeyFromToggleId(checkbox.id);
                    this.set(key, checkbox.checked);
                }, { passive: false });
                
                // Also handle click events on the container
                toggleSwitch.addEventListener('click', (e) => {
                    e.preventDefault();
                    console.log('Toggle switch click fired for:', checkbox.id); // Debug log
                    checkbox.checked = !checkbox.checked;
                    const key = this.getSettingKeyFromToggleId(checkbox.id);
                    this.set(key, checkbox.checked);
                });
            }
            
            // Improve accessibility
            checkbox.setAttribute('aria-label', checkbox.id.replace('-toggle', '').replace('-', ' '));
        });
        
        // Volume sliders (both main settings and game settings)
        const volumeSliders = document.querySelectorAll('input[type="range"]');
        volumeSliders.forEach(slider => {
            const rangeSlider = /** @type {HTMLInputElement} */ (slider);
            // Handle input events (for both mouse and touch)
            rangeSlider.addEventListener('input', (e) => {
                e.preventDefault();
                const volume = parseInt(rangeSlider.value);
                this.set('masterVolume', volume);
                this.updateVolumeDisplay(volume);
            });
            
            // Handle change events (for when user finishes dragging)
            rangeSlider.addEventListener('change', (e) => {
                e.preventDefault();
                const volume = parseInt(rangeSlider.value);
                this.set('masterVolume', volume);
                this.updateVolumeDisplay(volume);
            });
            
            // Handle touch events specifically for mobile
            rangeSlider.addEventListener('touchstart', (e) => {
                // Allow touch interaction
                e.stopPropagation();
            }, { passive: true });
            
            rangeSlider.addEventListener('touchmove', (e) => {
                // Prevent scrolling while dragging slider
                e.preventDefault();
            }, { passive: false });
            
            rangeSlider.addEventListener('touchend', (e) => {
                // Ensure final value is set
                e.preventDefault();
                const volume = parseInt(rangeSlider.value);
                this.set('masterVolume', volume);
                this.updateVolumeDisplay(volume);
            }, { passive: false });
            
            // Improve accessibility
            rangeSlider.setAttribute('aria-label', 'Master volume control');
            rangeSlider.setAttribute('role', 'slider');
        });
        
        // Reset button
        const resetButton = document.getElementById('reset-settings');
        if (resetButton) {
            resetButton.addEventListener('click', () => {
                this.resetToDefaults();
            });
            
            // Handle touch events for better mobile interaction
            resetButton.addEventListener('touchstart', (e) => {
                e.stopPropagation();
            }, { passive: true });
            
            resetButton.addEventListener('touchend', (e) => {
                e.preventDefault();
                this.resetToDefaults();
            }, { passive: false });
            
            // Improve accessibility
            resetButton.setAttribute('aria-label', 'Reset all settings to default values');
        }
        
        // Update UI with current settings
        this.updateUI();
    }
    
    /**
     * Set difficulty and update UI
     * @param {string} difficulty - Difficulty level
     */
    setDifficulty(difficulty) {
        this.set('difficulty', difficulty);
        
        // Update UI
        document.querySelectorAll('.difficulty-btn').forEach(btn => {
            btn.classList.remove('active');
        });
        document.querySelector(`[data-difficulty="${difficulty}"]`).classList.add('active');
    }
    
    /**
     * Update the settings UI with current values
     */
    /**
     * Update UI elements to reflect current settings
     */
    updateUI() {
        // Update checkboxes and sliders
        Object.keys(this.settings).forEach(key => {
            const element = document.getElementById(key);
            if (element) {
                const inputElement = /** @type {HTMLInputElement} */ (element);
                if (inputElement.type === 'checkbox') {
                    inputElement.checked = this.settings[key];
                } else if (inputElement.type === 'range') {
                    inputElement.value = this.settings[key];
                }
                
                // Update any associated labels
                const label = document.querySelector(`label[for="${key}"]`);
                if (label && inputElement.type === 'range') {
                    label.textContent = `${label.textContent.split(':')[0]}: ${inputElement.value}`;
                }
            }
        });
        // Difficulty (both main settings and game settings)
        document.querySelectorAll('.difficulty-btn').forEach(btn => {
            btn.classList.remove('active');
        });
        document.querySelectorAll(`[data-difficulty="${this.settings.difficulty}"]`).forEach(btn => {
            btn.classList.add('active');
        });
        
        // Toggles (both main settings and game settings)
        const toggleIds = [
            'auto-fire-toggle', 'game-auto-fire-toggle',
            'manual-aim-toggle', 'game-manual-aim-toggle',
            'screen-shake-toggle', 'game-screen-shake-toggle',
            'particles-toggle', 'game-particles-toggle',
            'engine-glow-toggle', 'game-engine-glow-toggle',
            'fps-toggle', 'game-fps-toggle',
            'music-toggle', 'game-music-toggle',
            'sfx-toggle', 'game-sfx-toggle'
        ];
        
        toggleIds.forEach(id => {
            const element = document.getElementById(id);
            if (element) {
                const inputElement = /** @type {HTMLInputElement} */ (element);
                const key = this.getSettingKeyFromToggleId(id);
                inputElement.checked = this.settings[key];
            }
        });
        
        // Volume (both main settings and game settings)
        const volumeSliders = document.querySelectorAll('input[type="range"]');
        volumeSliders.forEach(slider => {
            const rangeSlider = /** @type {HTMLInputElement} */ (slider);
            rangeSlider.value = this.settings.masterVolume.toString();
        });
        this.updateVolumeDisplay(this.settings.masterVolume);
    }
    
    /**
     * Update volume display
     * @param {number} volume - Volume level
     */
    updateVolumeDisplay(volume) {
        const volumeValue = document.querySelector('.volume-value');
        if (volumeValue) {
            volumeValue.textContent = `${volume}%`;
        }
    }
    
    /**
     * Reset all settings to defaults
     */
    resetToDefaults() {
        this.settings = {
            difficulty: 'NORMAL',
            autoFire: true,
            manualAim: false,
            screenShake: true,
            particles: true,
            engineGlow: true,
            fpsCounter: false,
            masterVolume: 80,
            music: true,
            sfx: true
        };
        
        this.saveSettings();
        this.updateUI();
        
        // Apply all settings
        Object.keys(this.settings).forEach(key => {
            this.applySetting(key, this.settings[key]);
        });
        
        // Show confirmation
        this.showNotification('Settings reset to defaults!');
    }
    
    /**
     * Show a notification
     * @param {string} message - Notification message
     */
    showNotification(message) {
        // Create notification element
        const notification = document.createElement('div');
        notification.className = 'settings-notification';
        notification.textContent = message;
        notification.style.cssText = `
            position: fixed;
            top: 20px;
            right: 20px;
            background: linear-gradient(135deg, #66ccff, #9966ff);
            color: white;
            padding: 12px 20px;
            border-radius: 25px;
            font-family: 'Orbitron', 'Exo 2', sans-serif;
            font-weight: 600;
            font-size: 14px;
            z-index: 10000;
            box-shadow: 0 4px 15px rgba(102, 204, 255, 0.4);
            animation: slideInRight 0.3s ease-out;
        `;
        
        document.body.appendChild(notification);
        
        // Remove after 3 seconds
        setTimeout(() => {
            notification.style.animation = 'slideOutRight 0.3s ease-in';
            setTimeout(() => {
                if (notification.parentNode) {
                    notification.parentNode.removeChild(notification);
                }
            }, 300);
        }, 3000);
    }
    
    /**
     * Get all settings as an object
     * @returns {Object} All settings
     */
    getAllSettings() {
        return { ...this.settings };
    }
}

// Create global settings manager instance
const settingsManager = new SettingsManager();

// Export for ES modules
export { SettingsManager, settingsManager };
