// @ts-check
import * as PIXI from 'pixi.js';
import { config } from '../config/game.js';

class PixiBullet {
    constructor(ship, pixiApp, width, height) {
        this.pixiApp = pixiApp;
        this.appWidth = width;
        this.appHeight = height;
        this.x = ship.x + ship.width;
        this.y = ship.y + ship.height / 2;
        
        var bulletRadiusPercent = config.BULLET_RADIUS_PERCENT;
        if (this.appWidth > 1000) {
            bulletRadiusPercent = 0.0050;
        }
        
        // Calculate size as percentage of canvas for full responsiveness
        this.width = Math.max(this.appWidth * config.BULLET_WIDTH_PERCENT, 2);
        this.height = Math.max(this.appHeight * config.BULLET_HEIGHT_PERCENT, 1);
        this.radius = Math.max(this.appWidth * bulletRadiusPercent, 2);
        this.innerRadius = Math.max(this.appWidth * config.BULLET_INNER_RADIUS_PERCENT, 1);
        
        // Calculate speed as percentage of canvas width for responsive behavior
        this.speed = config.getBulletSpeed(this.appWidth);
        
        // Create PixiJS graphics for bullet
        this.graphics = new PIXI.Graphics();
        this.updateGraphics();
        
        // Create container
        this.container = new PIXI.Container();
        this.container.addChild(this.graphics);
        this.container.x = this.x;
        this.container.y = this.y;
    }
    
    updateGraphics() {
        // Ensure complete cleanup before redrawing
        this.graphics.clear();
        
        // Add glow effect using multiple circles for depth
        this.graphics.beginFill(0x66CCFF, 0.3);
        this.graphics.drawCircle(0, 0, this.radius + 3);
        this.graphics.endFill();
        
        // Outer glow
        this.graphics.beginFill(0x66CCFF);
        this.graphics.drawCircle(0, 0, this.radius);
        this.graphics.endFill();
        
        // Bright center
        this.graphics.beginFill(0xFFFFFF);
        this.graphics.drawCircle(0, 0, this.innerRadius);
        this.graphics.endFill();
    }

    update(deltaMultiplier = 1) {
        this.x += this.speed * deltaMultiplier;
        this.container.x = this.x;
    }

    isOffScreen(canvas) {
        return this.x > (canvas || { width: this.appWidth }).width;
    }

    reset(ship, width, height) {
        this.x = ship.x + ship.width;
        this.y = ship.y + ship.height / 2;
        this.appWidth = width || this.appWidth;
        this.appHeight = height || this.appHeight;
        
        // Recalculate size and speed for new canvas
        this.width = Math.max(this.appWidth * config.BULLET_WIDTH_PERCENT, 5);
        this.height = Math.max(this.appHeight * config.BULLET_HEIGHT_PERCENT, 2);
        this.radius = Math.max(this.appWidth * config.BULLET_RADIUS_PERCENT, 4);
        this.innerRadius = Math.max(this.appWidth * config.BULLET_INNER_RADIUS_PERCENT, 2);
        this.speed = config.getBulletSpeed(this.appWidth);
        
        // Recreate graphics if they were destroyed
        if (!this.graphics) {
            this.graphics = new PIXI.Graphics();
            if (this.container) {
                this.container.addChild(this.graphics);
            }
        }
        
        // Recreate container if it was destroyed
        if (!this.container) {
            this.container = new PIXI.Container();
            this.container.addChild(this.graphics);
        }
        
        // Reset container position and visibility
        this.container.x = this.x;
        this.container.y = this.y;
        this.container.visible = true;
        this.container.alpha = 1.0;
        
        // Clear and update graphics to prevent visual artifacts
        this.graphics.clear();
        this.updateGraphics();
    }
    
    // Method to get the PixiJS container for adding to stage
    getContainer() {
        return this.container;
    }
    
    // Method to destroy the bullet
    destroy() {
        this.container.destroy();
    }
}

// Export the class for ES modules
export { PixiBullet };
