// @ts-check
import { rng } from '../../utils/rng.js';
import { Particle } from './Particle.js';

class ParticleSystem {
  constructor() {
    this.particles = [];
    this.pool = [];
    this.poolSize = 200;
    this.enabled = true;

    // Initialize pool
    for (let i = 0; i < this.poolSize; i++) {
      this.pool.push(new Particle({ x: 0, y: 0 }));
    }
  }
  
  setEnabled(enabled) {
    this.enabled = enabled;
    if (!enabled) {
      // Clear all particles when disabled
      this.particles = [];
    }
  }

  getParticle(options) {
    let particle;
    if (this.pool.length > 0) {
      particle = this.pool.pop();
      particle.reset(options);
    } else {
      particle = new Particle(options);
    }
    return particle;
  }

  returnToPool(particle) {
    particle.isDead = true;
    this.pool.push(particle);
  }

  // Explosion effect for obstacle destruction
  explosion(x, y, color = '#9966ff', count = 20) {
    if (!this.enabled) return;
    
    for (let i = 0; i < count; i++) {
      const angle = (Math.PI * 2 * i) / count;
      const speed = rng.randomFloat(2, 5);
      const particle = this.getParticle({
        x,
        y,
        vx: Math.cos(angle) * speed,
        vy: Math.sin(angle) * speed,
        life: rng.randomFloat(0.3, 0.8),
        size: rng.randomFloat(1, 3),
        color,
        type: 'circle',
      });
      this.particles.push(particle);
    }
  }

  // Enhanced hit sparks for bullet impacts with realistic physics
  hitSparks(x, y, color = '#66ccff') {
    if (!this.enabled) return;
    
    // Create main spark burst with varied properties
    this.createSparkBurst(x, y, color, 15, 3, 10, 0.3, 0.7, 1.5, 4.5);
    
    // Create fast white sparks for high-energy impact
    this.createSparkBurst(x, y, '#FFFFFF', 8, 8, 15, 0.15, 0.4, 0.8, 2.5);
    
    // Create slow glowing sparks for lingering effect
    this.createSparkBurst(x, y, color, 6, 1, 5, 0.5, 1.0, 2, 6);
    
    // Create tiny micro-sparks for detail
    this.createSparkBurst(x, y, '#FFDD88', 10, 12, 20, 0.08, 0.25, 0.3, 1.2);
    
    // Add directional sparks based on bullet trajectory
    this.createDirectionalSparks(x, y, color);
  }
  
  // Helper method to create spark bursts with different properties
  createSparkBurst(x, y, color, count, minSpeed, maxSpeed, minLife, maxLife, minSize, maxSize) {
    for (let i = 0; i < count; i++) {
      const angle = rng.randomFloat(0, Math.PI * 2);
      const speed = rng.randomFloat(minSpeed, maxSpeed);
      const life = rng.randomFloat(minLife, maxLife);
      const size = rng.randomFloat(minSize, maxSize);
      
      const particle = this.getParticle({
        x: x + rng.randomFloat(-3, 3), // Increased spread
        y: y + rng.randomFloat(-3, 3),
        vx: Math.cos(angle) * speed,
        vy: Math.sin(angle) * speed,
        life: life,
        size: size,
        color: color,
        type: 'circle',
        gravity: rng.randomFloat(0.15, 0.4), // Enhanced gravity
        friction: rng.randomFloat(0.95, 0.98), // Air resistance
        rotation: rng.randomFloat(0, Math.PI * 2), // Random rotation
        rotationSpeed: rng.randomFloat(-0.3, 0.3), // Rotation speed
      });
      this.particles.push(particle);
    }
  }
  
  // Create directional sparks that follow bullet trajectory
  createDirectionalSparks(x, y, color) {
    // Get bullet direction from the last bullet that hit
    const bulletDirection = this.lastBulletDirection || { x: 1, y: 0 };
    
    // Create sparks in the direction of bullet travel
    for (let i = 0; i < 5; i++) {
      const spread = rng.randomFloat(-0.5, 0.5); // Small spread around direction
      const angle = Math.atan2(bulletDirection.y, bulletDirection.x) + spread;
      const speed = rng.randomFloat(8, 15);
      const life = rng.randomFloat(0.2, 0.5);
      const size = rng.randomFloat(1, 3);
      
      const particle = this.getParticle({
        x: x + rng.randomFloat(-1, 1),
        y: y + rng.randomFloat(-1, 1),
        vx: Math.cos(angle) * speed,
        vy: Math.sin(angle) * speed,
        life: life,
        size: size,
        color: color,
        type: 'circle',
        gravity: rng.randomFloat(0.1, 0.2),
        friction: rng.randomFloat(0.96, 0.99),
      });
      this.particles.push(particle);
    }
  }
  
  // Store bullet direction for directional sparks
  setBulletDirection(direction) {
    this.lastBulletDirection = direction;
  }

  // Hit flash for bullet impact (keeping original for compatibility)
  hitFlash(x, y, color = '#66ccff') {
    if (!this.enabled) return;
    
    for (let i = 0; i < 4; i++) {
      const angle = rng.randomFloat(0, Math.PI * 2);
      const speed = rng.randomFloat(3, 5);
      const particle = this.getParticle({
        x,
        y,
        vx: Math.cos(angle) * speed,
        vy: Math.sin(angle) * speed,
        life: 0.3,
        size: 2,
        color,
        type: 'circle',
      });
      this.particles.push(particle);
    }
  }

  // Thrust particles for ship engine
  thrust(x, y, intensity = 1) {
    if (!this.enabled) return;
    
    const count = Math.floor(intensity * 3);
    for (let i = 0; i < count; i++) {
      const particle = this.getParticle({
        x: x + rng.randomFloat(-5, 5),
        y: y + rng.randomFloat(-5, 5),
        vx: rng.randomFloat(-8, -3),
        vy: rng.randomFloat(-2, 2),
        life: rng.randomFloat(0.2, 0.5),
        size: rng.randomFloat(2, 4),
        color: rng.randomChoice(['#66ccff', '#9966ff', '#ff66cc']),
        alpha: 0.8,
        type: 'circle',
      });
      this.particles.push(particle);
    }
  }

  // Energy trail for bullets
  bulletTrail(x, y) {
    if (!this.enabled) return;
    
    const particle = this.getParticle({
      x,
      y,
      vx: rng.randomFloat(-1, 0),
      vy: rng.randomFloat(-0.5, 0.5),
      life: 0.3,
      size: 2,
      color: '#66ccff',
      alpha: 0.6,
      type: 'circle',
    });
    this.particles.push(particle);
  }

  // Damage sparks for ship hit
  damageSparks(x, y, color = '#ff3333') {
    if (!this.enabled) return;
    
    for (let i = 0; i < 12; i++) {
      const angle = rng.randomFloat(0, Math.PI * 2);
      const speed = rng.randomFloat(4, 10);
      const particle = this.getParticle({
        x,
        y,
        vx: Math.cos(angle) * speed,
        vy: Math.sin(angle) * speed,
        life: 0.6,
        size: 2,
        color,
        gravity: 0.2,
        type: 'line',
      });
      this.particles.push(particle);
    }
  }

  update(deltaMultiplier = 1) {
    if (!this.enabled) return;
    
    for (let i = this.particles.length - 1; i >= 0; i--) {
      const particle = this.particles[i];
      particle.update(deltaMultiplier);

      if (particle.isDead) {
        this.returnToPool(particle);
        this.particles.splice(i, 1);
      }
    }
  }

  draw(ctx) {
    if (!this.enabled) return;
    this.particles.forEach(particle => particle.draw(ctx));
  }

  clear() {
    this.particles.forEach(particle => this.returnToPool(particle));
    this.particles = [];
  }
}

// Create global particle system
const particleSystem = new ParticleSystem();

// Export for ES modules
export { Particle, ParticleSystem, particleSystem };
