// @ts-check
import * as PIXI from 'pixi.js';
import { config } from '../config/game.js';
import { rng } from '../utils/rng.js';

class PixiObstacle {
    constructor(pixiApp, gameState, frameCount, width, height) {
        this.id = Date.now() + Math.random(); // Unique ID
        this.pixiApp = pixiApp;
        this.gameState = gameState;
        this.appWidth = width;
        this.appHeight = height;
        
        // Calculate obstacle dimensions as percentage of canvas for full responsiveness
        const minTopHeight = height * config.OBSTACLE_MIN_TOP_HEIGHT;
        const topVariation = height * config.OBSTACLE_TOP_VARIATION;
        const gapSize = height * config.OBSTACLE_GAP_SIZE;

        this.top = Math.floor(rng.random() * topVariation) + minTopHeight;
        this.bottom = height - (this.top + gapSize);
        this.x = width;
        this.width = Math.max(width * config.OBSTACLE_WIDTH_PERCENT, config.OBSTACLE_MIN_WIDTH);
        
        // Calculate speeds as percentage of canvas width for consistent experience
        this.speed = config.getObstacleSpeed(width);
        this.bulletSpeed = config.getObstacleBulletSpeed(width);
        
        this.hasGun = rng.random() < config.OBSTACLE_GUN_CHANCE;
        this.lastShot = frameCount;
        this.enemyBullets = [];
        this.passed = false;
        this.isDestroying = false;
        this.destructionProgress = 0;
        this.destructionSpeed = 0.1;
        
        // Visual design properties
        this.type = rng.randomInt(0, 4); // 4 different visual styles
        this.animationOffset = rng.random() * Math.PI * 2; // Random animation start
        this.particles = [];
        this.crystalRotation = 0;
        this.energyPulse = 0;
        
        // Create PixiJS container
        this.container = new PIXI.Container();
        this.container.x = this.x;
        
        // Create graphics for different parts
        this.topGraphics = new PIXI.Graphics();
        this.bottomGraphics = new PIXI.Graphics();
        this.gunGraphics = new PIXI.Graphics();
        this.bulletGraphics = new PIXI.Graphics();
        
        this.container.addChild(this.topGraphics);
        this.container.addChild(this.bottomGraphics);
        this.container.addChild(this.gunGraphics);
        this.container.addChild(this.bulletGraphics);
        
        // Initialize particles for energy effects
        this.initializeParticles();
        
        // Draw initial graphics
        this.updateGraphics();
    }

    initializeParticles() {
        // Create floating energy particles
        const particleRange = this.appHeight * 0.167; // 16.7% of canvas height
        const baseSpeed = this.appHeight * 0.00083; // Base speed relative to canvas height
        const maxSpeedVariation = this.appHeight * 0.0025; // Speed variation relative to canvas height
        const minSize = Math.max(this.appWidth * 0.0025, 1); // Min particle size
        const maxSize = Math.max(this.appWidth * 0.0075, 3); // Max particle size
        
        for (let i = 0; i < config.PARTICLE_COUNT; i++) {
            this.particles.push({
                offsetY: rng.random() * particleRange,
                offsetX: rng.random() * this.width,
                speed: baseSpeed + rng.random() * maxSpeedVariation,
                size: minSize + rng.random() * (maxSize - minSize),
                alpha: 0.3 + rng.random() * 0.7
            });
        }
    }

    update(frameCount, deltaMultiplier = 1) {
        // Update destruction progress if being destroyed
        if (this.isDestroying) {
            this.destructionProgress += this.destructionSpeed * deltaMultiplier;
            this.updateGraphics();
            return;
        }
        
        this.x -= this.speed * deltaMultiplier;
        this.container.x = this.x;
        
        // Update animation properties
        this.crystalRotation += 0.02 * deltaMultiplier;
        this.energyPulse = Math.sin(frameCount * 0.05 + this.animationOffset) * 0.5 + 0.5;
        
        // Update particles
        this.particles.forEach(p => {
            p.offsetY += p.speed * deltaMultiplier;
            if (p.offsetY > this.top) {
                p.offsetY = 0;
            }
        });

        // Update enemy bullets
        for (let i = this.enemyBullets.length - 1; i >= 0; i--) {
            this.enemyBullets[i].x -= this.bulletSpeed * deltaMultiplier;
            this.enemyBullets[i].y += Math.sin(this.enemyBullets[i].x / 50) * 2 * deltaMultiplier; // Wavy bullet pattern

            // Remove bullets that are off screen
            if (this.enemyBullets[i].x < 0) {
                this.enemyBullets.splice(i, 1);
            }
        }

        // Shoot if has gun
        if (this.hasGun && frameCount - this.lastShot > config.OBSTACLE_SHOOT_INTERVAL) {
            this.enemyBullets.push({
                x: this.x + this.width/2,
                y: this.top + (this.appHeight * 0.033) // 3.3% of canvas height
            });
            this.lastShot = frameCount;
        }
        
        // Update graphics
        this.updateGraphics();
    }
    
    updateGraphics() {
        // Clear all graphics
        this.topGraphics.clear();
        this.bottomGraphics.clear();
        this.gunGraphics.clear();
        this.bulletGraphics.clear();
        
        // Don't draw if fully destroyed
        if (this.destructionProgress >= 1) return;

        // Create destruction effect
        if (this.isDestroying) {
            this.drawDestructionEffect();
            this.destructionProgress += this.destructionSpeed;
            return;
        }

        // Draw different obstacle types
        switch(this.type) {
            case 0:
                this.drawCrystalBarrier();
                break;
            case 1:
                this.drawEnergyGate();
                break;
            case 2:
                this.drawPlasmaBeams();
                break;
            case 3:
                this.drawHolographicWalls();
                break;
        }

        // Draw machine gun if pipe has one
        if (this.hasGun) {
            this.drawGun();
        }

        // Draw enemy bullets
        this.drawEnemyBullets();
    }
    
    drawCrystalBarrier() {
        // Cyberpunk energy barrier with hexagonal patterns
        const hexSize = this.appHeight * 0.02;
        const numHexes = Math.floor(this.top / (hexSize * 1.5));
        
        // Top barrier - Hexagonal energy field
        for (let i = 0; i < numHexes; i++) {
            const y = (this.top / numHexes) * i + hexSize;
            const phase = (this.crystalRotation + i * 0.5) * 2;
            const alpha = 0.6 + Math.sin(phase) * 0.4;
            const glowIntensity = 0.3 + this.energyPulse * 0.7;
            
            this.drawHexagon(this.topGraphics, this.width/2, y, hexSize, 0x00FFFF, alpha, glowIntensity);
        }
        
        // Bottom barrier
        for (let i = 0; i < numHexes; i++) {
            const y = this.appHeight - this.bottom + (this.bottom / numHexes) * i + hexSize;
            const phase = (-this.crystalRotation - i * 0.5) * 2;
            const alpha = 0.6 + Math.sin(phase) * 0.4;
            const glowIntensity = 0.3 + this.energyPulse * 0.7;
            
            this.drawHexagon(this.bottomGraphics, this.width/2, y, hexSize, 0xFF00FF, alpha, glowIntensity);
        }
        
        // Energy connection beams with data stream effect
        this.drawDataStream(this.topGraphics, 0, this.top, 0x00FFFF);
        this.drawDataStream(this.bottomGraphics, 0, this.appHeight - this.bottom, 0xFF00FF);
        
        // Cyberpunk scanlines
        this.drawScanlines(this.topGraphics, 0, this.top);
        this.drawScanlines(this.bottomGraphics, this.appHeight - this.bottom, this.appHeight);
    }
    
    drawHexagon(graphics, x, y, size, color, alpha, glowIntensity) {
        // Outer glow
        graphics.beginFill(color, alpha * glowIntensity);
        graphics.poly([
            x, y - size * 1.2,
            x + size * 0.6, y - size * 0.6,
            x + size * 0.6, y + size * 0.6,
            x, y + size * 1.2,
            x - size * 0.6, y + size * 0.6,
            x - size * 0.6, y - size * 0.6
        ]);
        graphics.endFill();
        
        // Inner hexagon
        graphics.beginFill(color, alpha);
        graphics.poly([
            x, y - size,
            x + size * 0.5, y - size * 0.5,
            x + size * 0.5, y + size * 0.5,
            x, y + size,
            x - size * 0.5, y + size * 0.5,
            x - size * 0.5, y - size * 0.5
        ]);
        graphics.endFill();
        
        // Core energy
        graphics.beginFill(0xFFFFFF, alpha * 0.8);
        graphics.drawCircle(x, y, size * 0.3);
        graphics.endFill();
    }
    
    drawDataStream(graphics, startY, endY, color) {
        const streamWidth = 3;
        const numStreams = 3;
        
        for (let i = 0; i < numStreams; i++) {
            const x = (this.width / (numStreams + 1)) * (i + 1);
            const phase = this.energyPulse * Math.PI * 2 + i * 0.5;
            const alpha = 0.4 + Math.sin(phase) * 0.3;
            
            graphics.setStrokeStyle({ width: streamWidth, color: color, alpha: alpha });
            graphics.moveTo(x, startY);
            graphics.lineTo(x, endY);
            
            // Data particles
            for (let j = 0; j < 5; j++) {
                const particleY = startY + (endY - startY) * (j / 4) + Math.sin(phase + j) * 10;
                graphics.beginFill(color, alpha * 0.8);
                graphics.drawCircle(x, particleY, 2);
                graphics.endFill();
            }
        }
    }
    
    drawScanlines(graphics, startY, endY) {
        const lineCount = Math.floor((endY - startY) / 4);
        
        for (let i = 0; i < lineCount; i++) {
            const y = startY + (i * 4);
            const alpha = 0.1 + Math.sin(this.energyPulse * Math.PI * 2 + i * 0.3) * 0.1;
            
            graphics.setStrokeStyle({ width: 1, color: 0x00FFFF, alpha: alpha });
            graphics.moveTo(0, y);
            graphics.lineTo(this.width, y);
        }
    }
    
    drawEnergyMatrix(graphics, startY, endY, color) {
        const gridSize = 20;
        const rows = Math.floor((endY - startY) / gridSize);
        const cols = Math.floor(this.width / gridSize);
        
        for (let row = 0; row < rows; row++) {
            for (let col = 0; col < cols; col++) {
                const x = col * gridSize;
                const y = startY + row * gridSize;
                const phase = this.energyPulse * Math.PI * 2 + (row + col) * 0.3;
                const alpha = 0.2 + Math.sin(phase) * 0.3;
                
                if (Math.random() < 0.3) {
                    graphics.beginFill(color, alpha);
                    graphics.drawRect(x, y, gridSize * 0.8, gridSize * 0.8);
                    graphics.endFill();
                }
            }
        }
    }
    
    drawCircuitPattern(graphics, startY, endY) {
        const circuitWidth = 2;
        const numCircuits = 5;
        
        for (let i = 0; i < numCircuits; i++) {
            const x = (this.width / numCircuits) * i;
            const phase = this.energyPulse * Math.PI * 2 + i * 0.5;
            const alpha = 0.4 + Math.sin(phase) * 0.3;
            
            graphics.setStrokeStyle({ width: circuitWidth, color: 0x00FFFF, alpha: alpha });
            graphics.moveTo(x, startY);
            graphics.lineTo(x + 10, startY + 10);
            graphics.lineTo(x + 20, startY);
            graphics.lineTo(x + 30, startY + 15);
            graphics.lineTo(x + 40, startY);
            graphics.lineTo(x + 50, startY + 20);
            graphics.lineTo(x + 60, startY);
            graphics.lineTo(x + 70, startY + 10);
            graphics.lineTo(x + 80, startY);
            graphics.lineTo(x + 90, startY + 5);
            graphics.lineTo(x + 100, startY);
        }
    }
    
    drawHolographicData(graphics, startY, endY) {
        const dataHeight = endY - startY;
        const numDataLines = 8;
        
        for (let i = 0; i < numDataLines; i++) {
            const x = (this.width / numDataLines) * i;
            const phase = this.energyPulse * Math.PI * 2 + i * 0.8;
            const alpha = 0.3 + Math.sin(phase) * 0.4;
            
            // Binary data stream
            for (let j = 0; j < dataHeight; j += 8) {
                const y = startY + j;
                const bitValue = Math.sin(phase + j * 0.1) > 0 ? 1 : 0;
                
                if (bitValue) {
                    graphics.beginFill(0x00FFFF, alpha);
                    graphics.drawRect(x, y, 3, 3);
                    graphics.endFill();
                }
            }
        }
    }
    
    drawNeonBorder(graphics, x, y, width, height, color) {
        // Outer glow
        graphics.setStrokeStyle({ width: 6, color: color, alpha: 0.3 });
        graphics.rect(x, y, width, height);
        
        // Main border
        graphics.setStrokeStyle({ width: 3, color: color, alpha: 0.8 });
        graphics.rect(x + 3, y + 3, width - 6, height - 6);
        
        // Inner highlight
        graphics.setStrokeStyle({ width: 1, color: 0xFFFFFF, alpha: 0.6 });
        graphics.rect(x + 6, y + 6, width - 12, height - 12);
    }
    
    drawLaserGrid(graphics, startY, endY, color) {
        const gridSize = 30;
        const rows = Math.floor((endY - startY) / gridSize);
        const cols = Math.floor(this.width / gridSize);
        
        // Vertical lasers
        for (let col = 0; col <= cols; col++) {
            const x = col * gridSize;
            const phase = this.energyPulse * Math.PI * 2 + col * 0.2;
            const alpha = 0.4 + Math.sin(phase) * 0.3;
            
            graphics.setStrokeStyle({ width: 2, color: color, alpha: alpha });
            graphics.moveTo(x, startY);
            graphics.lineTo(x, endY);
        }
        
        // Horizontal lasers
        for (let row = 0; row <= rows; row++) {
            const y = startY + row * gridSize;
            const phase = this.energyPulse * Math.PI * 2 + row * 0.2;
            const alpha = 0.4 + Math.sin(phase) * 0.3;
            
            graphics.setStrokeStyle({ width: 2, color: color, alpha: alpha });
            graphics.moveTo(0, y);
            graphics.lineTo(this.width, y);
        }
    }
    
    drawEnergyNodes(graphics, startY, endY) {
        const nodeSpacing = 40;
        const numNodes = Math.floor(this.width / nodeSpacing);
        
        for (let i = 0; i < numNodes; i++) {
            const x = i * nodeSpacing + nodeSpacing / 2;
            const y = startY + (endY - startY) / 2;
            const phase = this.energyPulse * Math.PI * 2 + i * 0.5;
            const alpha = 0.6 + Math.sin(phase) * 0.4;
            const size = 8 + Math.sin(phase) * 4;
            
            // Outer glow
            graphics.beginFill(0x00FFFF, alpha * 0.3);
            graphics.drawCircle(x, y, size * 1.5);
            graphics.endFill();
            
            // Main node
            graphics.beginFill(0x00FFFF, alpha);
            graphics.drawCircle(x, y, size);
            graphics.endFill();
            
            // Core
            graphics.beginFill(0xFFFFFF, alpha * 0.8);
            graphics.drawCircle(x, y, size * 0.4);
            graphics.endFill();
        }
    }
    
    drawPlasmaDistortion(graphics, startY, endY) {
        const distortionLines = 5;
        
        for (let i = 0; i < distortionLines; i++) {
            const x = (this.width / distortionLines) * i;
            const phase = this.energyPulse * Math.PI * 2 + i * 0.8;
            const alpha = 0.2 + Math.sin(phase) * 0.2;
            
            graphics.setStrokeStyle({ width: 1, color: 0xFF00FF, alpha: alpha });
            graphics.moveTo(x, startY);
            
            // Wavy distortion line
            for (let j = 0; j < endY - startY; j += 5) {
                const y = startY + j;
                const waveOffset = Math.sin(phase + j * 0.1) * 10;
                graphics.lineTo(x + waveOffset, y);
            }
        }
    }
    
    drawHolographicProjection(graphics, startY, endY) {
        const projectionHeight = endY - startY;
        const numLayers = 3;
        
        for (let layer = 0; layer < numLayers; layer++) {
            const layerAlpha = 0.2 + (layer * 0.1);
            const phase = this.energyPulse * Math.PI * 2 + layer * 0.5;
            const color = layer === 0 ? 0x00FFFF : layer === 1 ? 0xFF00FF : 0xFFFF00;
            
            // Holographic scanlines
            const scanlineCount = Math.floor(projectionHeight / 6);
            for (let i = 0; i < scanlineCount; i++) {
                const y = startY + (i * 6);
                const glitchOffset = Math.sin(phase + i * 0.3) * 5;
                const alpha = layerAlpha + Math.sin(phase + i * 0.2) * 0.1;
                
                graphics.beginFill(color, alpha);
                graphics.drawRect(glitchOffset, y, this.width, 3);
                graphics.endFill();
            }
        }
    }
    
    drawDigitalGlitch(graphics, startY, endY) {
        const glitchBlocks = 8;
        const blockHeight = (endY - startY) / glitchBlocks;
        
        for (let i = 0; i < glitchBlocks; i++) {
            const y = startY + (i * blockHeight);
            const phase = this.energyPulse * Math.PI * 2 + i * 0.8;
            const glitchIntensity = Math.sin(phase) * 0.5 + 0.5;
            
            if (glitchIntensity > 0.7) {
                const glitchOffset = Math.sin(phase) * 15;
                const alpha = glitchIntensity * 0.3;
                
                graphics.beginFill(0xFF0000, alpha);
                graphics.drawRect(glitchOffset, y, this.width * 0.3, blockHeight);
                graphics.endFill();
                
                graphics.beginFill(0x00FF00, alpha);
                graphics.drawRect(this.width * 0.7 + glitchOffset, y, this.width * 0.3, blockHeight);
                graphics.endFill();
            }
        }
    }
    
    drawHolographicBorder(graphics, x, y, width, height) {
        // Outer energy field
        graphics.setStrokeStyle({ width: 4, color: 0x00FFFF, alpha: 0.3 });
        graphics.rect(x, y, width, height);
        
        // Main holographic border
        graphics.setStrokeStyle({ width: 2, color: 0x00FFFF, alpha: 0.6 });
        graphics.rect(x + 2, y + 2, width - 4, height - 4);
        
        // Inner scanlines
        const scanlineCount = Math.floor(height / 8);
        for (let i = 0; i < scanlineCount; i++) {
            const scanY = y + (i * 8);
            const alpha = 0.1 + Math.sin(this.energyPulse * Math.PI * 2 + i * 0.5) * 0.1;
            
            graphics.setStrokeStyle({ width: 1, color: 0x00FFFF, alpha: alpha });
            graphics.moveTo(x + 4, scanY);
            graphics.lineTo(x + width - 4, scanY);
        }
    }
    
    drawEnergyGate() {
        // Cyberpunk energy matrix with circuit patterns
        this.drawEnergyMatrix(this.topGraphics, 0, this.top, 0x00FF88);
        this.drawEnergyMatrix(this.bottomGraphics, this.appHeight - this.bottom, this.appHeight, 0xFF0088);
        
        // Circuit board patterns
        this.drawCircuitPattern(this.topGraphics, 0, this.top);
        this.drawCircuitPattern(this.bottomGraphics, this.appHeight - this.bottom, this.appHeight);
        
        // Holographic data streams
        this.drawHolographicData(this.topGraphics, 0, this.top);
        this.drawHolographicData(this.bottomGraphics, this.appHeight - this.bottom, this.appHeight);
        
        // Energy field borders with neon glow
        this.drawNeonBorder(this.topGraphics, 0, 0, this.width, this.top, 0x00FF88);
        this.drawNeonBorder(this.bottomGraphics, 0, this.appHeight - this.bottom, this.width, this.bottom, 0xFF0088);
    }
    
    drawPlasmaBeams() {
        // Cyberpunk laser grid with energy nodes
        this.drawLaserGrid(this.topGraphics, 0, this.top, 0xFF0088);
        this.drawLaserGrid(this.bottomGraphics, this.appHeight - this.bottom, this.appHeight, 0x8800FF);
        
        // Energy nodes and connections
        this.drawEnergyNodes(this.topGraphics, 0, this.top);
        this.drawEnergyNodes(this.bottomGraphics, this.appHeight - this.bottom, this.appHeight);
        
        // Plasma distortion effects
        this.drawPlasmaDistortion(this.topGraphics, 0, this.top);
        this.drawPlasmaDistortion(this.bottomGraphics, this.appHeight - this.bottom, this.appHeight);
    }
    
    drawHolographicWalls() {
        // Cyberpunk holographic projection with glitch effects
        this.drawHolographicProjection(this.topGraphics, 0, this.top);
        this.drawHolographicProjection(this.bottomGraphics, this.appHeight - this.bottom, this.appHeight);
        
        // Digital glitch effects
        this.drawDigitalGlitch(this.topGraphics, 0, this.top);
        this.drawDigitalGlitch(this.bottomGraphics, this.appHeight - this.bottom, this.appHeight);
        
        // Holographic borders with energy field
        this.drawHolographicBorder(this.topGraphics, 0, 0, this.width, this.top);
        this.drawHolographicBorder(this.bottomGraphics, 0, this.appHeight - this.bottom, this.width, this.bottom);
    }
    
    drawDestructionEffect() {
        const segments = 8;
        const segmentHeight = (this.top / segments);
        const bottomSegmentHeight = (this.bottom / segments);
        
        // Draw top pipe segments with spread effect
        for (let i = 0; i < segments; i++) {
            const spread = (this.destructionProgress * 50) * (i / segments);
            const alpha = 1 - this.destructionProgress;
            
            this.topGraphics.alpha = alpha;
            this.topGraphics.beginFill(0x6A3A7A);
            this.topGraphics.drawRect(
                spread, 
                i * segmentHeight, 
                this.width, 
                segmentHeight
            );
            this.topGraphics.endFill();
            
            // Bottom pipe segment
            this.bottomGraphics.alpha = alpha;
            this.bottomGraphics.beginFill(0x6A3A7A);
            this.bottomGraphics.drawRect(
                -spread,
                this.appHeight - this.bottom + (i * bottomSegmentHeight),
                this.width,
                bottomSegmentHeight
            );
            this.bottomGraphics.endFill();
        }
    }
    
    drawGun() {
        const centerX = this.width/2;
        const gunY = this.top;
        
        // Cyberpunk energy cannon with advanced targeting
        this.drawEnergyCannon(centerX, gunY);
        this.drawTargetingSystem(centerX, gunY);
        this.drawEnergyCharging(centerX, gunY);
    }
    
    drawEnergyCannon(centerX, gunY) {
        // Main cannon body with cyberpunk design
        this.gunGraphics.beginFill(0x333333);
        this.gunGraphics.drawRect(centerX - 15, gunY, 30, 20);
        this.gunGraphics.endFill();
        
        // Energy core
        const coreAlpha = 0.6 + this.energyPulse * 0.4;
        this.gunGraphics.beginFill(0x00FFFF, coreAlpha);
        this.gunGraphics.drawRect(centerX - 8, gunY + 5, 16, 10);
        this.gunGraphics.endFill();
        
        // Energy barrel
        this.gunGraphics.beginFill(0x666666);
        this.gunGraphics.drawRect(centerX - 3, gunY + 20, 6, 15);
        this.gunGraphics.endFill();
        
        // Barrel energy field
        this.gunGraphics.beginFill(0x00FFFF, coreAlpha * 0.5);
        this.gunGraphics.drawRect(centerX - 2, gunY + 20, 4, 15);
        this.gunGraphics.endFill();
    }
    
    drawTargetingSystem(centerX, gunY) {
        // Targeting reticle
        const reticleSize = 12 + this.energyPulse * 6;
        const reticleAlpha = 0.8 + this.energyPulse * 0.2;
        
        this.gunGraphics.setStrokeStyle({ width: 2, color: 0xFF0000, alpha: reticleAlpha });
        this.gunGraphics.circle(centerX, gunY - 8, reticleSize);
        
        // Crosshairs
        this.gunGraphics.setStrokeStyle({ width: 1, color: 0xFF0000, alpha: reticleAlpha });
        this.gunGraphics.moveTo(centerX - reticleSize, gunY - 8);
        this.gunGraphics.lineTo(centerX + reticleSize, gunY - 8);
        this.gunGraphics.moveTo(centerX, gunY - 8 - reticleSize);
        this.gunGraphics.lineTo(centerX, gunY - 8 + reticleSize);
        
        // Targeting data
        this.gunGraphics.setStrokeStyle({ width: 1, color: 0x00FFFF, alpha: 0.6 });
        this.gunGraphics.moveTo(centerX - 20, gunY - 15);
        this.gunGraphics.lineTo(centerX - 10, gunY - 10);
        this.gunGraphics.moveTo(centerX + 20, gunY - 15);
        this.gunGraphics.lineTo(centerX + 10, gunY - 10);
    }
    
    drawEnergyCharging(centerX, gunY) {
        // Energy charging effect
        const chargeIntensity = this.energyPulse;
        const chargeAlpha = 0.4 + chargeIntensity * 0.6;
        
        // Charging particles
        for (let i = 0; i < 5; i++) {
            const particleX = centerX + (Math.random() - 0.5) * 20;
            const particleY = gunY + 25 + Math.random() * 10;
            const particleSize = 2 + Math.random() * 3;
            
            this.gunGraphics.beginFill(0x00FFFF, chargeAlpha);
            this.gunGraphics.drawCircle(particleX, particleY, particleSize);
            this.gunGraphics.endFill();
        }
        
        // Energy beam preview
        if (chargeIntensity > 0.7) {
            this.gunGraphics.setStrokeStyle({ width: 3, color: 0x00FFFF, alpha: chargeAlpha });
            this.gunGraphics.moveTo(centerX, gunY + 35);
            this.gunGraphics.lineTo(centerX, gunY + 50);
        }
    }
    
    drawEnemyBullets() {
        this.enemyBullets.forEach(bullet => {
            const bulletX = bullet.x - this.x;
            const bulletY = bullet.y;
            
            // Energy bullet with cyberpunk effects
            this.drawEnergyBullet(bulletX, bulletY);
        });
    }
    
    drawEnergyBullet(x, y) {
        const bulletSize = 6;
        const energyAlpha = 0.8 + this.energyPulse * 0.2;
        
        // Outer energy field
        this.bulletGraphics.beginFill(0xFF0088, energyAlpha * 0.3);
        this.bulletGraphics.drawCircle(x, y, bulletSize * 1.5);
        this.bulletGraphics.endFill();
        
        // Main bullet body
        this.bulletGraphics.beginFill(0xFF0088, energyAlpha);
        this.bulletGraphics.drawCircle(x, y, bulletSize);
        this.bulletGraphics.endFill();
        
        // Energy core
        this.bulletGraphics.beginFill(0xFFFFFF, energyAlpha * 0.8);
        this.bulletGraphics.drawCircle(x, y, bulletSize * 0.4);
        this.bulletGraphics.endFill();
        
        // Energy trail
        this.bulletGraphics.setStrokeStyle({ width: 2, color: 0xFF0088, alpha: energyAlpha * 0.5 });
        this.bulletGraphics.moveTo(x - 10, y);
        this.bulletGraphics.lineTo(x - 5, y);
        
        // Energy particles
        for (let i = 0; i < 3; i++) {
            const particleX = x + (Math.random() - 0.5) * 8;
            const particleY = y + (Math.random() - 0.5) * 8;
            const particleSize = 1 + Math.random() * 2;
            
            this.bulletGraphics.beginFill(0x00FFFF, energyAlpha * 0.6);
            this.bulletGraphics.drawCircle(particleX, particleY, particleSize);
            this.bulletGraphics.endFill();
        }
    }

    startDestruction() {
        this.isDestroying = true;
    }

    isFullyDestroyed() {
        return this.destructionProgress >= 1;
    }

    isOffScreen() {
        return this.x + this.width < 0;
    }

    checkCollision(ship) {
        const bounds = ship.getBounds();
        return (bounds.x + bounds.width > this.x &&
                bounds.x < this.x + this.width &&
                (bounds.y < this.top || 
                 bounds.y + bounds.height > this.appHeight - this.bottom));
    }

    checkBulletCollision(bullet) {
        return (bullet.x < this.x + this.width &&
                bullet.x + bullet.width > this.x &&
                (bullet.y < this.top || bullet.y > this.appHeight - this.bottom));
    }

    checkEnemyBulletCollision(ship) {
        const bounds = ship.getBounds();
        for (let bullet of this.enemyBullets) {
            if (bullet.x > bounds.x && 
                bullet.x < bounds.x + bounds.width &&
                bullet.y > bounds.y &&
                bullet.y < bounds.y + bounds.height) {
                return true;
            }
        }
        return false;
    }

    reset(pixiApp, gameState, frameCount, width, height) {
        this.id = Date.now() + rng.random();
        this.pixiApp = pixiApp;
        this.gameState = gameState;
        this.appWidth = width;
        this.appHeight = height;
        
        // Recalculate dimensions
        const minTopHeight = height * config.OBSTACLE_MIN_TOP_HEIGHT;
        const topVariation = height * config.OBSTACLE_TOP_VARIATION;
        const gapSize = height * config.OBSTACLE_GAP_SIZE;
        
        this.top = Math.floor(rng.random() * topVariation) + minTopHeight;
        this.bottom = height - (this.top + gapSize);
        this.x = width;
        this.width = Math.max(width * config.OBSTACLE_WIDTH_PERCENT, config.OBSTACLE_MIN_WIDTH);
        
        this.speed = config.getObstacleSpeed(width);
        this.bulletSpeed = config.getObstacleBulletSpeed(width);
        
        this.hasGun = rng.random() < config.OBSTACLE_GUN_CHANCE;
        this.lastShot = frameCount;
        this.enemyBullets = [];
        this.passed = false;
        this.isDestroying = false;
        this.destructionProgress = 0;
        
        this.type = rng.randomInt(0, 4);
        this.animationOffset = rng.random() * Math.PI * 2;
        this.particles = [];
        this.crystalRotation = 0;
        this.energyPulse = 0;
        
        // Reset container position and visibility
        this.container.x = this.x;
        this.container.visible = true;
        this.container.alpha = 1.0;
        
        // Clear and reset graphics
        this.topGraphics.clear();
        this.bottomGraphics.clear();
        this.gunGraphics.clear();
        this.bulletGraphics.clear();
        
        this.initializeParticles();
        this.updateGraphics();
    }
    
    // Method to get the PixiJS container for adding to stage
    getContainer() {
        return this.container;
    }
    
    // Method to destroy the obstacle
    destroy() {
        this.container.destroy();
    }
}

// Export the class for ES modules
export { PixiObstacle };
