// @ts-check
import { config } from '../../config/game.js';
import { rng } from '../../utils/rng.js';

class Renderer {
    constructor(canvas, ctx, internalCanvas = null, internalCtx = null) {
        this.canvas = canvas;
        this.ctx = ctx;
        this.internalCanvas = internalCanvas || canvas;
        this.internalCtx = internalCtx || ctx;
        this.stars = this.createStars();
        
        // Offscreen canvas for background caching
        this.backgroundCanvas = document.createElement('canvas');
        this.backgroundCtx = this.backgroundCanvas.getContext('2d');
        this.backgroundDirty = true;
        this.setupBackgroundCanvas();
    }

    setupBackgroundCanvas() {
        this.backgroundCanvas.width = this.internalCanvas.width;
        this.backgroundCanvas.height = this.internalCanvas.height;
        this.backgroundDirty = true;
    }

    createStars() {
        return Array(config.STAR_COUNT).fill().map(() => ({
            x: rng.random() * this.internalCanvas.width,
            y: rng.random() * this.internalCanvas.height,
            size: rng.randomFloat(config.STAR_MIN_SIZE, config.STAR_MAX_SIZE),
            speed: rng.randomFloat(config.STAR_MIN_SPEED, config.STAR_MAX_SPEED),
            color: this.getStarColor()
        }));
    }

    getStarColor() {
        const colors = [
            'rgba(255, 255, 255, 0.9)',    // White
            'rgba(102, 204, 255, 0.8)',    // Cyan
            'rgba(153, 102, 255, 0.7)',    // Purple
            'rgba(255, 102, 204, 0.7)',    // Pink
        ];
        return rng.randomChoice(colors);
    }

    drawStars() {
        // Update star positions
        this.stars.forEach(star => {
            star.x -= star.speed;
            if (star.x < 0) star.x = this.internalCanvas.width;
        });

        // Draw stars to offscreen canvas if dirty
        if (this.backgroundDirty) {
            this.backgroundCtx.clearRect(0, 0, this.backgroundCanvas.width, this.backgroundCanvas.height);
            this.stars.forEach(star => {
                this.backgroundCtx.fillStyle = star.color;
                this.backgroundCtx.shadowBlur = 3;
                this.backgroundCtx.shadowColor = star.color;
                this.backgroundCtx.beginPath();
                this.backgroundCtx.arc(star.x, star.y, star.size, 0, Math.PI * 2);
                this.backgroundCtx.fill();
            });
            this.backgroundCtx.shadowBlur = 0;
            this.backgroundDirty = false;
        }

        // Blit the cached background
        this.internalCtx.drawImage(this.backgroundCanvas, 0, 0);
    }

    clear() {
        // Draw gradient background to internal canvas
        const gradient = this.internalCtx.createLinearGradient(0, 0, 0, this.internalCanvas.height);
        gradient.addColorStop(0, '#0a0a1a');
        gradient.addColorStop(0.5, '#1a1a3a');
        gradient.addColorStop(1, '#0a1a2a');
        this.internalCtx.fillStyle = gradient;
        this.internalCtx.fillRect(0, 0, this.internalCanvas.width, this.internalCanvas.height);
        
        // Mark background as dirty for next frame
        this.backgroundDirty = true;
    }

    resetStars() {
        this.stars.forEach(star => {
            star.x = rng.random() * this.internalCanvas.width;
            star.y = rng.random() * this.internalCanvas.height;
            star.color = this.getStarColor();
        });
        this.backgroundDirty = true;
    }
}

// Export the class for ES modules
export { Renderer };


